import { Component, OnInit } from '@angular/core';
import { UserService } from '../services/user.service';
import { FormBuilder, FormGroup, Validators, FormArray } from '@angular/forms';

@Component({
  selector: 'app-user-profile',
  templateUrl: './user-profile.component.html',
  styleUrls: ['./user-profile.component.css']
})
export class UserProfileComponent implements OnInit {
  public currentUser: any;
  profileForm: FormGroup;

  constructor(
    private userService: UserService,
    private formBuilder: FormBuilder
  ) { }

  ngOnInit() {
    debugger;
    this.profileForm = this.formBuilder.group({
      email: [{value: '', disabled: true}, Validators.required],
      name: [{ value: '', disabled: true }, Validators.required],
      //rate: [{ value: '', disabled: true }, Validators.required],
      role: [{ value: '', disabled: true }, Validators.required],
      sendEmail: ['', Validators.required]
    });

    this.currentUser = this.userService.getCurrentUser();
    let roleStr = "";
    roleStr += this.currentUser.localUser.userRoles.map(x => {
      return `${x.role.roleDescription}`;
    });
    this.profileForm.setValue({
      email: this.currentUser.localUser.email,
      name: this.currentUser.localUser.name,
      //rate: this.currentUser.localUser.rate,
      role: roleStr,
      sendEmail: this.currentUser.localUser.emailToSupervisor == null ? false : this.currentUser.localUser.emailToSupervisor
    });

  }

  onSubmit() {
    if (this.profileForm.invalid) {
      return;
    }
    this.userService.updateProfile(this.profileForm.get("sendEmail").value).then(x => {
      if (x.successfully) {
        this.currentUser.localUser.emailToSupervisor = x.data.emailToSupervisor;
        this.userService.setUser(this.currentUser);
      }
    });
  }
}
