import { Injectable, Inject } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { throwError } from 'rxjs';
import { UserService } from '../../app/services/user.service';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class CommissionService {
  private baseApiUrl: string;

  constructor(
    private http: HttpClient, @Inject('BASE_API_URL') baseApiUrl: string,
    private userService: UserService,
    private router: Router
  ) {
    this.baseApiUrl = baseApiUrl;
  }

  //GET
  async getCommissionList(): Promise<ResponseApi> {
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}Commission/GetCommissionList`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }
  
  async getCommissionDistributions(idCommission): Promise<ResponseApi> {
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}CommissionDistribution/GetCommissionDistribution?idCommission=${idCommission}`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //POST
  createCommission(commissionName: string): Promise<ResponseApi> {
    let commission = { "commissionName": commissionName };
    try {
      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}Commission/CreateCommission`, JSON.stringify(commission), this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  createCommissionDistribution(distribForm: any, idCommission: number): Promise<ResponseApi> {
    let commissionDistrib = { "idParent": +distribForm.idParent, "nameDistribution": distribForm.nameDistrib, "percentage": +distribForm.percentage  };
    try {
      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}CommissionDistribution/CreateCommissionDistribution?idCommission=${idCommission}`, JSON.stringify(commissionDistrib), this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  //DELETE
  async DeleteCommission(idCommission): Promise<ResponseApi> {
    try {
      let response = await this.http
        .delete(`${this.baseApiUrl}Commission/DeleteCommission?idCommission=${idCommission}`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  async DeleteCommissionDistribution(idCommissionDistribution: number, idCommission: number ): Promise<ResponseApi> {
    try {
      let response = await this.http
        .delete(`${this.baseApiUrl}CommissionDistribution/DeleteCommissionDistribution?idCommissionDistribution=${idCommissionDistribution}&idCommission=${idCommission}`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //PUT
  updateCommission(idCommission: number, commissionName: string): Promise<ResponseApi> {
    let commissionUpd = { "idCommission": +idCommission, "commissionName": commissionName };
    try {
      let response = this.http
        .put<ResponseApi>(`${this.baseApiUrl}Commission/UpdateCommission`, JSON.stringify(commissionUpd), this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  //PUT
  updateCommissionDistribution(distribForm: any, idCommissionDistrib: number): Promise<ResponseApi> {
    let commissionDistrib = { "idCommissionDistribution": +idCommissionDistrib, "idParent": +distribForm.idParent, "nameDistribution": distribForm.nameDistrib, "percentage": +distribForm.percentage };
    try {
      let response = this.http
        .put<ResponseApi>(`${this.baseApiUrl}CommissionDistribution/UpdateCommissionDistribution`, JSON.stringify(commissionDistrib), this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  // Error handling
  errorHandl(error) {
    let errorMessage = '';
    if (error.error instanceof ErrorEvent) {
      // Get client-side error
      errorMessage = error.error.message;
    } else {
      // Get server-side error
      errorMessage = `Error Code: ${error.status}\nMessage: ${error.message}`;
    }
    console.log(errorMessage);

    if (error.status == 401) {
      this.userService.signOut();
      this.router.navigate(['login']);
    }

    return throwError(errorMessage);
  }
}

interface ResponseApi {
  successfully: boolean
  reason: string
  data: any
}
