import { Injectable, Inject } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { UserService } from '../../app/services/user.service';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class BusinessQuoteService {
  private baseApiUrl: string;

  constructor(
    private http: HttpClient, @Inject('BASE_API_URL') baseApiUrl: string,
    private userService: UserService,
    private router: Router
  ) {
    this.baseApiUrl = baseApiUrl;
  }

  //GET
  async GetBusinessQuoteById(idBusinessQuote: number): Promise<ResponseApi> {
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}BusinessQuote/GetBusinessQuoteById?idBusinessQuote=${idBusinessQuote}`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }
  //GET
  async GetBusinessQuoteByGuidValidate(guidValidate: string): Promise<ResponseApi> {
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}BusinessQuote/GetBusinessQuoteByGuidValidate?guidValidate=${guidValidate}`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  async AcceptBusinessQuoteByGuidValidate(guidValidate: string, clientComments: string): Promise<ResponseApi> {
    try {
      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}BusinessQuote/AcceptBusinessQuoteByGuidValidate?guidValidate=${guidValidate}&clientComments=${clientComments}`, {}, this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  async RejectionBusinessQuoteByGuidValidate(guidValidate: string, clientComments: string): Promise<ResponseApi> {
    try {
      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}BusinessQuote/RejectionBusinessQuoteByGuidValidate?guidValidate=${guidValidate}&clientComments=${clientComments}`, {}, this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  async GeneratePDFBusinessQuote(idBusinessQuote: number): Promise<ResponseApi> {
    try {
      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}BusinessQuote/GeneratePdfBusinessQuote?idBusinessQuote=${idBusinessQuote}`, {}, this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  async SendPDFBusinessQuote(idBusinessQuote: number): Promise<ResponseApi> {
    try {
      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}BusinessQuote/SendBusinessQuote?idBusinessQuote=${idBusinessQuote}`, {}, this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  async CancelBusinessQuote(idBusinessQuote: number): Promise<ResponseApi> {
    try {
      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}BusinessQuote/CancelBusinessQuote?idBusinessQuote=${idBusinessQuote}`, {}, this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  //GET
  async GetPendingBusinessQuoteByExecutive(): Promise<ResponseApi> {
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}BusinessQuote/GetPendingBusinessQuoteByExecutive`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //GET
  async GetPendingBusinessQuoteBySupervisor(): Promise<ResponseApi> {
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}BusinessQuote/GetPendingBusinessQuoteBySupervisor`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //POST
  createBusinessQuote(businessQuoteForm: any, needValidation: boolean, idUser, detail: any[]): Promise<ResponseApi> {
    let businessQuote = this.buildJson(businessQuoteForm, needValidation, idUser, detail);
    try {
      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}BusinessQuote/CreateBusinessQuote`, JSON.stringify(businessQuote), this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  createBusinessQuoteAndDownload(businessQuoteForm: any, needValidation: boolean, idUser, detail: any[]): Promise<ResponseApi> {
    let businessQuote = this.buildJson(businessQuoteForm, needValidation, idUser, detail);
    try {
      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}BusinessQuote/CreateAndDownloadBusinessQuote`, JSON.stringify(businessQuote), this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  //PUT
  updateStateBusinessQuote(idBusinessQuote: number, state: number): Promise<ResponseApi> {
    try {
      let response = this.http
        .put<ResponseApi>(`${this.baseApiUrl}BusinessQuote/UpdateStateBusinessQuote?idBusinessQuote=${idBusinessQuote}&state=${state}`, null, this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  //PUT
  updateBusinessQuote(businessQuoteForm: any, businessQuote: any, detail: any[]): Promise<ResponseApi> {
    let businessQuoteUpdate = this.builUpdateJson(businessQuoteForm, businessQuote, detail);
    try {
      let response = this.http
        .put<ResponseApi>(`${this.baseApiUrl}BusinessQuote/UpdateBusinessQuote`, JSON.stringify(businessQuoteUpdate), this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  //POST
  issueBusinessQuote(idbusinessQuote: number): Promise<ResponseApi> {
    try {
      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}BusinessQuote/IssueBusinessQuote?idBusinessQuote=${idbusinessQuote}`, null, this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  //DELETE
  deleteBusinessQuoteDetail(businessQuoteDetail): Promise<ResponseApi> {
    const httpOptionsDelete = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${this.userService.getToken()}`
      }),
      body: JSON.stringify(businessQuoteDetail)
    };
    try {
      let response = this.http
        .delete<ResponseApi>(`${this.baseApiUrl}BusinessQuoteDetail/DeleteBusinessQuoteDetail`, httpOptionsDelete)
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  //GET
  async getBusinessQuoteDetailByCommission(idPremiumCommission): Promise<ResponseApi> {
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}BusinessQuoteDetail/GetBusinessQuoteDetailByCommission?idPremiumCommission=${idPremiumCommission}`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //GET
  async getBusinessQuoteListByFilter(filter): Promise<ResponseApi> {
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}BusinessQuote/GetBusinessQuoteListByFilter?filter=${filter}`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  builUpdateJson(businessQuoteForm: any, businessQuote: any, detail: any[]) {
    let dateOfIssue = this.buildDateShipping(businessQuoteForm.dateOfIssue);
    return {
      "idBusinessQuote": businessQuote.idBusinessQuote,
      "idCustomer": parseInt(businessQuoteForm.customer.toString()),
      "idUser": businessQuote.idUser,
      "dateOfIssue": dateOfIssue,
      "provider": businessQuoteForm.provider.toString(),
      "categoryGood": businessQuoteForm.categoryGood.toString(),
      "availableCoverage": businessQuoteForm.available.toString(),
      "state": businessQuote.state,
      "updateDate": this.currentDate(),
      "businessQuoteDetails": this.buildDetailJson(detail, businessQuote.idBusinessQuote)
    }
  }

  buildJson(businessQuoteForm: any, needValidation: boolean, idUser, detail: any[]) {
    let dateOfIssue = this.buildDateShipping(businessQuoteForm.dateOfIssue);
    let state = needValidation ? 2 : 1;
    return {
      "idCustomer": parseInt(businessQuoteForm.customer.toString()),
      "idUser": idUser,
      "dateOfIssue": dateOfIssue,
      "createDate": this.currentDate(),
      "provider": businessQuoteForm.provider.toString(),
      "categoryGood": businessQuoteForm.categoryGood.toString(),
      "availableCoverage": businessQuoteForm.available.toString(),
      "state": state,
      "businessQuoteDetails": this.buildDetailJson(detail)
    }
  }

  buildDetailJson(detail: any[], idBusinessQuote = 0) {
    return detail.map(x => {
      return {
        "idBusinessQuote": +idBusinessQuote,
        "idProvider": +x.idProvider,
        "idAvailableCoverage": +x.idAvailableCoverage,
        "idCategoryGood": +x.idCategoryGood,
        "rateValue": this.removeFormatNumber(x.rateValue)
      };
    });
  }
  
  removeFormatNumber(value) {
    return parseFloat(value.toString().replace(',', ''));
  }

  currentDate() {
    let today = new Date();
    return today.getFullYear() + '-' + this.zfill(today.getMonth() + 1, 2) + '-' + this.zfill(today.getDate(), 2);
  }

  buildDateShipping(date) {
    return date.year + '-' + this.zfill(date.month, 2) + '-' + this.zfill(date.day, 2);
  }

  zfill(number, width) {
    var numberOutput = Math.abs(number);
    var length = number.toString().length;
    var zero = "0";
    if (width <= length)
      return numberOutput.toString();
    else
      return ((zero.repeat(width - length)) + numberOutput.toString());
  }

  // Error handling
  errorHandl(error) {
    let errorMessage = '';
    if (error.error instanceof ErrorEvent) {
      // Get client-side error
      errorMessage = error.error.message;
    } else {
      // Get server-side error
      errorMessage = `Error Code: ${error.status}\nMessage: ${error.message}`;
    }
    console.log(errorMessage);

    if (error.status == 401) {
      this.userService.signOut();
      this.router.navigate(['login']);
    }

    return throwError(errorMessage);
  }
}

interface ResponseApi {
  successfully: boolean
  reason: string
  data: any
  dataOptional: any[]
}
