import { Component, OnInit } from '@angular/core';
import { UserService } from '../../app/services/user.service';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { BusinessQuoteService } from '../services/business-quote.service';
import { FormControl } from '@angular/forms';
import { BusinessQuoteModalComponent } from '../business-quote-modal/business-quote-modal.component';

@Component({
  selector: 'app-rate-request',
  templateUrl: './rate-request.component.html',
  styleUrls: ['./rate-request.component.css']
})
export class RateRequestComponent implements OnInit {
  public currentUser: any;
  public userRole: string;
  public businessQuote;
  public businessQuoteFilter;
  public userId: number;
  filter = new FormControl('');

  constructor(
    private userService: UserService,
    private businessQuoteService: BusinessQuoteService,
    private modalBusinessQuote: NgbModal
  ) { }

  ngOnInit() {
    this.currentUser = this.userService.getCurrentUser();
    this.verifyUserRole();
    this.loadData();
  }

  loadData() {
    if (this.userRole.indexOf('Supervisor') != -1) {
      this.businessQuoteService.GetPendingBusinessQuoteBySupervisor().then(x => {
        this.processData(x.data);
      });
    } else if (this.userRole.indexOf('Executive') != -1) {
        this.businessQuoteService.GetPendingBusinessQuoteByExecutive().then(x => {
          this.processData(x.data);
        });
    }
  }

  processData(data) {
    debugger;
    this.businessQuote = data;
    this.businessQuoteFilter = data;
  }

  verifyUserRole() {
    if (this.currentUser != null) {
      this.userId = this.currentUser.localUser.idUser;
      if (this.currentUser.localUser != null && this.currentUser.localUser.userRoles != null) {
        this.userRole = "";
        this.userRole += this.currentUser.localUser.userRoles.map(x => {
          return `${x.role.roleDescription}-`;
        });
      } else {
        this.userRole = "";
      }
    }
  }

  Approve(idBusinessQuote) {
    this.businessQuoteService.updateStateBusinessQuote(idBusinessQuote, 3).then(x => {
      if (x.successfully) {
        this.loadData();
      }
    });
  }

  Reject(idBusinessQuote) {
    this.businessQuoteService.updateStateBusinessQuote(idBusinessQuote, 4).then(x => {
      if (x.successfully) {
        this.loadData();
      }
    });
  }

  Edit(idBusinessQuote, idExecutive, readOnly) {
    const modalRef = this.modalBusinessQuote.open(BusinessQuoteModalComponent, { size: 'lg', backdrop: 'static' });
    modalRef.componentInstance.isModal = true;
    modalRef.componentInstance.idBusinessQuote = idBusinessQuote;
    modalRef.componentInstance.idExecutive = idExecutive;
    modalRef.componentInstance.readOnly = readOnly;
    modalRef.result.then(() => { this.loadData(); }).catch(x => { });
  }

  Issue(idBusinessQuote) {
    this.businessQuoteService.issueBusinessQuote(idBusinessQuote).then(x => {
      if (x.successfully) {
        this.loadData();
      }
    });
  }
}
