import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from "@angular/router";
import { Router } from "@angular/router";
import { each } from 'jquery';
import { QuoteService } from '../services/quote.service';
import { ModalConfirmationComponent } from '../modal-confirmation/modal-confirmation.component'
import { NgbModal, ModalDismissReasons, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-review-quote',
  templateUrl: './review-quote.component.html',
  styleUrls: ['./review-quote.component.css']
})
export class ReviewQuoteComponent implements OnInit {
  public idQuote: string;
  public reviewQuotes: any;
  public result: boolean = false;
  public loading: boolean = false;
  public loadingPreview: boolean = false;
  public urlRedirect: string;
  modalSend: NgbModalRef;
  closeResult = '';

  constructor(
    private route: ActivatedRoute,
    public quoteService: QuoteService,
    private router: Router,
    private modalService: NgbModal,
    private modalConfirmation: NgbModal
  ) { }

  ngOnInit() {
    this.idQuote = this.route.snapshot.paramMap.get("quoteId");

    if (this.idQuote !== null) {
      this.getReviewQuote();
    }
  }

  getReviewQuote() {
    this.quoteService.getReviewQuoteInfo(this.idQuote)
      .then(x => {
        this.reviewQuotes = x.data;
        debugger;
        this.reviewQuotes.forEach(review => {
          if (review.QuoteIdProvider != null) {
            this.result = true;
          }
        });
      });
  }

  selectProvider(id) {
    //this.loading = true;
    //this.loadingPreview = false;
    //this.quoteService.AcceptQuote(+this.idQuote, id)
    //  .then(x => {
    //    this.loading = false;
    //    if (x.successfully == true) {
    //      this.result = true;
    //      this.urlRedirect = x.data.urlRedirect;
    //    }
    //  });


  }

  showSendMail(content, id) {
    this.modalSend = this.modalService.open(content, { ariaLabelledBy: 'modal-basic-title' });
    this.modalSend.result.then((result) => {
      debugger;
      var sendMail = 0;
      if (result == 'send') {
        sendMail = 1;
      } else if (result == 'dontSend') {
        sendMail = 0;
      }


      this.loading = true;
      this.loadingPreview = false;
      this.quoteService.AcceptQuote(this.idQuote, id, sendMail)
        .then(x => {
          this.loading = false;
          if (x.successfully == true) {
            this.result = true;
            this.urlRedirect = x.data.urlRedirect;
          }
        });

      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      debugger;
      this.closeResult = `dismiss`;
    });
  }

  previewProvider(id) {
    this.loading = true;
    this.loadingPreview = true;
    this.quoteService.PreviewQuote(this.idQuote, id)
      .then(x => {
        this.loading = false;
        if (x.successfully == true) {
          var sampleArr = this.base64ToArrayBuffer(Object.values(x.data)[0]);
          this.saveByteArray(Object.keys(x.data)[0], sampleArr);

          var sampleArr = this.base64ToArrayBuffer(Object.values(x.data)[1]);
          this.saveByteArray(Object.keys(x.data)[1], sampleArr);
        }
      });
  }

  base64ToArrayBuffer(base64) {
    var binaryString = window.atob(base64);
    var binaryLen = binaryString.length;
    var bytes = new Uint8Array(binaryLen);
    for (var i = 0; i < binaryLen; i++) {
      var ascii = binaryString.charCodeAt(i);
      bytes[i] = ascii;
    }
    return bytes;
  }

  saveByteArray(reportName, byte) {
    var blob = new Blob([byte], { type: "application/pdf" });
    var link = document.createElement('a');
    link.href = window.URL.createObjectURL(blob);
    var fileName = reportName;
    link.download = fileName;
    link.click();
  };

  generateCertificate() {
    this.router.navigate(['/certificate', this.idQuote]);
  }
}
