import { Component, OnInit, ElementRef } from '@angular/core';
import { QuoteFreeService } from '../services/quote-free.service';
import { CurrencyService } from '../services/currency.service';
import { CountryService } from '../services/country.service';
import { CategoryGoodService } from '../services/category-good.service';
import { InsuranceTypeService } from '../services/insurance-type.service';
import { QuoteService } from '../services/quote.service';
import { ShippingMethodService } from '../services/shipping-method.service';
import { AvailableCoverageService } from '../services/available-coverage.service';
import { InsuredService } from '../services/insured.service';
import { FormBuilder, FormGroup, Validators, FormArray } from '@angular/forms';
import { Options } from 'select2';
import { Router } from "@angular/router";
import { ActivatedRoute } from "@angular/router";
import { UserService } from '../services/user.service';
import { NgbModal, ModalDismissReasons, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';


@Component({
  selector: 'app-quickquote-free',
  templateUrl: './quickquote-free.component.html',
  styleUrls: ['./quickquote-free.component.css']
})
export class QuickquoteFreeComponent implements OnInit {
  public currencies: Currency[];
  public categoryGood: any = [];
  public insuranceTypes: any[];
  public availableCovers: any[];
  public shipmentMethodList: any[];
  public countries: any[];
  public premium: number;
  public quote;
  public currentSymbol: string;
  public currentUser: any;
  public idClient = 1;
  private guidUser: string;
  public classForm: string = '';
  public reviewQuotes: any;
  public idProviderSelect = null;
  public idInsuredSelect = null;
  public idQuoteCreated = null;
  public guidQuoteCreated = null;
  public deductibleDescriptionModal = null;
  public noEditInsured = false;
  public submittedInsured = false;
  modalSend: NgbModalRef;
  closeResult = '';
  submitted = false;
  loadingPdf = false;
  sending = false;
  quoteCreated = false;
  approveded = false;
  aproving = false;

  quickQuoteForm: FormGroup;
  optionsSelect: Options;
  insuredForm: FormGroup;
  insuredAction = "Save";
  insuredDataResult: any;


  constructor(
    public quoteFreeService: QuoteFreeService,
    public countryService: CountryService,
    public currencyService: CurrencyService,
    public categoryService: CategoryGoodService,
    public insuranceTypeService: InsuranceTypeService,
    public quoteService: QuoteService,
    public availableService: AvailableCoverageService,
    public shippingMethodService: ShippingMethodService,
    private formBuilder: FormBuilder,
    private router: Router,
    private userService: UserService,
    private route: ActivatedRoute,
    public insuredService: InsuredService,
    private modalService: NgbModal,
    private modalConfirmation: NgbModal
  ) { }

  ngOnInit() {
    this.guidUser = this.route.snapshot.paramMap.get("guidUser");

    this.quickQuoteForm = this.formBuilder.group({
      categoryGood: ['', Validators.required],
      shippingMethod: ['', Validators.required],
      available: ['', Validators.required],
      currency: ['', Validators.required],
      merchandiseValue: ['', Validators.required],
      freightValue: ['', Validators.required],
      insuranceLimit: ['', Validators.required],
      countryShipCommence: ['', Validators.required],
      countryDestination: ['', Validators.required]
    });

    this.insuredForm = this.formBuilder.group({
      name: ['', Validators.required],
      country: ['', Validators.required],
      city: ['', Validators.required],
      address: ['', Validators.required],
      phone: [''],
      email: ['', Validators.required],
      contactPerson: ['', Validators.required],
      taxId: ['', Validators.required],
      zipCode: ['', Validators.required],
      creditLimit: ['']
    });

    this.quoteFreeService.get()
      .then(x => {

        this.categoryGood = [{ id: '', text: '' }];
        x.data.lstCategoryGood.forEach(category => {
          this.categoryGood.push({ id: category.idCategoryGood, text: category.description });
        });
        this.insuranceTypes = x.data.lstInsuranceType;
        this.currencies = x.data.lstCurrency;
        this.shipmentMethodList = x.data.lstShippingMethod;
        this.availableCovers = x.data.lstAvailableCoverage;
        this.countries = x.data.lstCountry;

      });

    this.currentUser = this.userService.getCurrentUser();
    if (this.currentUser != null) {
      this.idClient = this.currentUser.localUser.idClient;
    }

    (this.quickQuoteForm as unknown as FormArray).valueChanges.subscribe(values => {
      this.premium = null;
      this.submitted = false;
    });

    this.optionsSelect = {
      placeholder: "Category of goods being shipped...",
      allowClear: true,
      width: "100%"
    };


  }

  reload() {
    document.location.reload(true);
  }

  // convenience getter for easy access to form fields
  get f() { return this.quickQuoteForm.controls; }

  get fi() { return this.insuredForm.controls; }

  onSubmit() {
    this.submitted = true;
    //this.classForm = "wait";

    if (this.quickQuoteForm.invalid) {
      return;
    }
    this.quoteFreeService.getQuoteRequested(this.quickQuoteForm.value, this.idClient).then(x => {
      //this.premium = x.data;
      this.classForm = "step2";
      this.reviewQuotes = x.data;

      setTimeout(x => {
        this.goToTopIsMobile(650);
      }, 500);
      //debugger;
      //this.reviewQuotes.forEach(review => {
      //  if (review.QuoteIdProvider != null) {
      //    this.result = true;
      //  }
      //});
    });

  }

  updateCurrentSymbol(event: any) {
    if (event.target.value != undefined && event.target.value != '')
      this.currentSymbol = this.currencies.find(x => x.idCurrency == event.target.value).symbol;
  }

  onReset() {
    this.classForm = "";
    this.submitted = false;
    this.quickQuoteForm.reset();
    this.reviewQuotes = null;
    this.premium = null;
    this.goToTopIsMobile();
  }

  //onFinish() {
  //  this.quoteService.finishQuickQuote(this.quickQuoteForm.value).then(x => {
  //    this.router.navigate(['/quote', x.data.guidQuickQuote]).then((e) => {
  //      if (e) {
  //        //console.log("Navigation is successful!");
  //      } else {
  //        //console.log("Navigation has failed!");
  //      }
  //    });
  //  });
  //}

  downloadPdf(idProvider) {
    this.loadingPdf = true;
    this.quoteFreeService.GeneratePDFFreeQuote(this.quickQuoteForm.value, this.idClient, idProvider).then(x => {
      this.loadingPdf = false;
      if (x.successfully == true) {
        var sampleArr = this.base64ToArrayBuffer(Object.values(x.data)[0]);
        this.saveByteArray(Object.keys(x.data)[0], sampleArr);

        var sampleArr = this.base64ToArrayBuffer(Object.values(x.data)[1]);
        this.saveByteArray(Object.keys(x.data)[1], sampleArr);
      }
    });
  }

  selectProvider(idProvider) {
    this.classForm = "step3";
    this.idProviderSelect = idProvider;
    setTimeout(x => {
      this.goToTopIsMobile(650);
    }, 500);
  }

  unSelectProvider() {
    this.classForm = "step2";
    this.idProviderSelect = null;
    setTimeout(x => {
      this.goToTopIsMobile(650);
    }, 500);
  }






  base64ToArrayBuffer(base64) {
    var binaryString = window.atob(base64);
    var binaryLen = binaryString.length;
    var bytes = new Uint8Array(binaryLen);
    for (var i = 0; i < binaryLen; i++) {
      var ascii = binaryString.charCodeAt(i);
      bytes[i] = ascii;
    }
    return bytes;
  }

  saveByteArray(reportName, byte) {
    var blob = new Blob([byte], { type: "application/pdf" });
    var link = document.createElement('a');
    link.href = window.URL.createObjectURL(blob);
    var fileName = reportName;
    link.download = fileName;
    link.click();
  };



  findByEmail() {
    let email = this.insuredForm.get("email").value;
    if (email != '') {
      this.quoteFreeService.getInsuredByEmail(email).then(res => {
        if (res.successfully && res.data != null) {
          this.idInsuredSelect = res.data.idInsured;
          this.setField(res.data);
          this.noEditInsured = true;
        } else {
          this.noEditInsured = false;
          this.setEmptyField();
        }
      });

    }
  }

  setEmptyField() {
    let email = this.insuredForm.get("email").value;
    this.insuredAction = 'Save';
    this.insuredForm.setValue({
      name: '',
      country: '',
      city: '',
      address: '',
      phone: '',
      contactPerson: '',
      taxId: '',
      email: email,
      creditLimit: 0
    });
  }

  setField(data) {
    this.insuredDataResult = data;
    this.idInsuredSelect = data.idInsured;
    this.insuredAction = 'Select';
    this.insuredForm.setValue({
      name: this.insuredDataResult.name,
      country: this.insuredDataResult.idCountry,
      city: this.insuredDataResult.city,
      address: this.insuredDataResult.address,
      phone: this.insuredDataResult.phone,
      email: this.insuredDataResult.email,
      contactPerson: this.insuredDataResult.contactPerson,
      taxId: this.insuredDataResult.taxId,
      zipCode: this.insuredDataResult.zipCode,
      creditLimit: this.insuredDataResult.creditLimit
    });
  }

  onSubmitFinish() {
    this.submittedInsured = true;
    
    if (!this.noEditInsured) { //si ya esta guardado, no revalidamos
      if (this.insuredForm.invalid) {
        return;
      }
    }

    this.sending = true;
    if (this.insuredAction == "Save") {
      this.quoteFreeService.createInsured(this.insuredForm.value).then(x => {
        if (x.successfully) {
          this.idInsuredSelect = x.data.idInsured;
          this.createQuote();
        }
      });
    } else {
      this.createQuote();
    }
  }

  createQuote() {
    this.sending = true;
    this.quoteFreeService.createQuote(this.quickQuoteForm.value, this.idClient, this.idProviderSelect, this.idInsuredSelect).then(x => {
      this.sending = false;
      if (x.successfully == true) {
        this.quoteCreated = true;
        this.idQuoteCreated = x.data.idQuote;
        this.guidQuoteCreated = x.data.guidQuote;
      }
    });
  }

  ApproveAndIssue(content, id) {
    this.modalSend = this.modalService.open(content, { ariaLabelledBy: 'modal-basic-title' });
    this.modalSend.result.then((result) => {
      debugger;
      var sendMail = 0;
      if (result == 'send') {
        sendMail = 1;
      } else if (result == 'dontSend') {
        sendMail = 0;
      }


      //this.loading = true;
      this.aproving = true;
      this.quoteService.AcceptQuote(this.guidQuoteCreated, this.idProviderSelect, sendMail)
        .then(x => {
          this.aproving = false;
          if (x.successfully == true) {
            this.approveded = true;
          }
        });

      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      debugger;
      this.closeResult = `dismiss`;
    });
  }

  showModalCondition(deductibleDescription, modalContent) {
    this.deductibleDescriptionModal = deductibleDescription;

    this.modalSend = this.modalService.open(modalContent, { ariaLabelledBy: 'modal-basic-title' });
    this.modalSend.result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `dismiss`;
    });
  }

  generateCertificate() {
    this.router.navigate(['/certificate', this.guidQuoteCreated]);
  }

  isMobile() {
    return (window.innerWidth < 600);
  }

  goToTopIsMobile(top = null) {
    if (this.isMobile()) {
      window.scroll({
        top: !!top ? top : 0,
        left: 0,
        behavior: 'smooth'
      });
    }
  }
}

interface Currency {
  idCurrency: number;
  description: string;
  symbol: string;
}
