import { Injectable, Inject } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { throwError } from 'rxjs';
import { UserService } from '../../app/services/user.service';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class CategoryGoodService {
  private baseApiUrl: string;
  public categoryGood: CategoryGood[];

  constructor(
    private http: HttpClient, @Inject('BASE_API_URL') baseApiUrl: string,
    private userService: UserService,
    private router: Router
  ) {
    this.baseApiUrl = baseApiUrl;
  }


  //GET
  async getCategoryGood(): Promise<ResponseApi> {
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}CategoryGood/GetCategoryGoodList`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      debugger;
      await this.errorHandl(error);
    }
  }

  //POST -- Create
  async createCategoryGood(categoryGoodForm: any): Promise<ResponseApi> {
    debugger;
    let categoryGood = { "description": categoryGoodForm.description, "coverageDescription": categoryGoodForm.coverageDescription };
    try {
      let response = await this.http
        .post<ResponseApi>(`${this.baseApiUrl}CategoryGood/`, JSON.stringify(categoryGood), this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      await this.errorHandl(error);
    }
  }


  //Put -- Create
  async updateCategoryGood(idCategoryGood: number, categoryGoodForm: any): Promise<ResponseApi> {
    let categoryGood = { "idCategoryGood": +idCategoryGood, "description": categoryGoodForm.description, "coverageDescription": categoryGoodForm.coverageDescription };
    try {
      let response = await this.http
        .put<ResponseApi>(`${this.baseApiUrl}CategoryGood/`, JSON.stringify(categoryGood), this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //DELETE
  async deleteCategoryGood(idCategoryGood): Promise<ResponseApi> {
    debugger;
    try {
      let response = await this.http
        .delete(`${this.baseApiUrl}CategoryGood?idCategoryGood=${idCategoryGood}`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  // Error handling
  errorHandl(error) {
    let errorMessage = '';
    if (error.error instanceof ErrorEvent) {
      // Get client-side error
      errorMessage = error.error.message;
    } else {
      // Get server-side error
      errorMessage = `Error Code: ${error.status}\nMessage: ${error.message}`;
    }
    console.log(errorMessage);

    if (error.status == 401) {
      this.userService.signOut();
      this.router.navigate(['login']);
    }

    return throwError(errorMessage);
  }
}

interface ResponseApi {
  successfully: boolean
  reason: string
  data: CategoryGood[]
}
interface CategoryGood {
  idCategoryGood: number;
  description: string
}
