import { Injectable, Inject } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { UserService } from '../../app/services/user.service';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class CustomerService {
  private baseApiUrl: string;

  constructor(
    private http: HttpClient, @Inject('BASE_API_URL') baseApiUrl: string,
    private userService: UserService,
    private router: Router
  ) {
    this.baseApiUrl = baseApiUrl;
  }

  //GET
  async getAllCustomerList(filter): Promise<ResponseApi> {
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}Customer/GetAllList?filter=${filter}`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //GET
  async getCustomerList(): Promise<ResponseApi> {
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}Customer/GetCustomerList`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //GET
  async getCustomerById(id): Promise<ResponseApi> {
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}Customer/GetCustomerById?idCustomer=${id}`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //GET
  async getCustomerByEmail(email): Promise<ResponseApi> {
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}Customer/GetCustomerByEmail?email=${email}`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //POST
  createCustomer(customerForm: any): Promise<ResponseApi> {
    let customer = this.buildJson(customerForm, null);
    try {
      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}Customer/CreateCustomer`, JSON.stringify(customer), this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  //POST
  updateCustomer(customerForm: any, idCustomer: number): Promise<ResponseApi> {
    let customer = this.buildJson(customerForm, idCustomer);
    try {
      let response = this.http
        .put<ResponseApi>(`${this.baseApiUrl}Customer/UpdateCustomer`, JSON.stringify(customer), this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  //POST
  updateProfile(customerForm: any): Promise<ResponseApi> {
    let customer = this.buildJsonProfile(customerForm);
    try {
      let response = this.http
        .put<ResponseApi>(`${this.baseApiUrl}Customer/UpdateProfile`, JSON.stringify(customer), this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  buildJson(customerForm: any, idCustomer: number) {

    return {
      "idCustomer": !!idCustomer ? idCustomer: 0,
      "name": customerForm.name,
      "idCountry": +customerForm.country,
      "city": customerForm.city,
      "address": customerForm.address,
      "phone": customerForm.phone.toString(),
      "email": customerForm.email,
      "contactPerson": customerForm.contactPerson,
      "taxId": customerForm.taxId,
      "creditLimit": customerForm.creditLimit,
      "inactivo": customerForm.inactivo,
    }
  }

  buildJsonProfile(customerForm: any) {

    return {
      "name": customerForm.name,
      "idCountry": +customerForm.country,
      "city": customerForm.city,
      "address": customerForm.address,
      "phone": customerForm.phone.toString(),
      "email": customerForm.email,
      "contactPerson": customerForm.contactPerson,
      "taxId": customerForm.taxId,
    }
  }

  // CustomreExecutive //
  //POST
  createCustomerExecutive(valueForm): Promise<ResponseApi> {
    debugger;
    let customerExecutive = { "idCustomer": +valueForm.customer, "idExecutive": +valueForm.executive };
    try {
      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}CustomerExecutive/CreateCustomerExecutive`, JSON.stringify(customerExecutive), this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  //GET
  async getCustomerExecutiveList(idCustomer: number = null): Promise<ResponseApi> {
    let idCustomerStr = '';
    if (!!idCustomer) idCustomerStr = `/${idCustomer}`;
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}CustomerExecutive/GetCustomerExecutiveList` + idCustomerStr, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //DELETE
  async deleteCustomerExecutive(idCustomer: number, idExecutive: number): Promise<ResponseApi> {
    try {
      let response = await this.http
        .delete(`${this.baseApiUrl}CustomerExecutive/DeleteCustomerExecutive?idCustomer=${idCustomer}&idExecutive=${idExecutive}`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }



  // CustomreUser //
  //POST
  createCustomerUser(valueForm): Promise<ResponseApi> {
    debugger;
    let customerUser = { "idCustomer": +valueForm.customer, "idUser": +valueForm.user };
    try {
      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}CustomerUser/CreateCustomerUser`, JSON.stringify(customerUser), this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  // CustomreUser //
  //POST
  updateCustomerUser(valueForm): Promise<ResponseApi> {
    try {
      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}CustomerUser/UpdateCustomerUser`, JSON.stringify(valueForm), this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  //GET
  async getCustomerUserList(idCustomer: number = null): Promise<ResponseApi> {
    let idCustomerStr = '';
    if (!!idCustomer) idCustomerStr = `/${idCustomer}`;
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}CustomerUser/GetCustomerUserList` + idCustomerStr, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //DELETE
  async deleteCustomerUser(idCustomer: number, idUser: number): Promise<ResponseApi> {
    try {
      let response = await this.http
        .delete(`${this.baseApiUrl}CustomerUser/DeleteCustomerUser?idCustomer=${idCustomer}&idUser=${idUser}`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }



  // CustomerInsured //
  //POST
  createCustomerInsured(valueForm): Promise<ResponseApi> {
    debugger;
    let customerinsured = { "idCustomer": +valueForm.customer, "idInsured": +valueForm.insured };
    try {
      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}CustomerInsured/CreateCustomerInsured`, JSON.stringify(customerinsured), this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  //GET
  async getCustomerInsuredList(idCustomer: number = null): Promise<ResponseApi> {
    let idCustomerStr = '';
    if (!!idCustomer) idCustomerStr = `/${idCustomer}`;
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}CustomerInsured/GetCustomerInsuredList` + idCustomerStr, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //DELETE
  async deleteCustomerInsured(idCustomer: number, idInsured: number): Promise<ResponseApi> {
    try {
      let response = await this.http
        .delete(`${this.baseApiUrl}CustomerInsured/DeleteCustomerInsured?idCustomer=${idCustomer}&idInsured=${idInsured}`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  // Error handling
  errorHandl(error) {
    let errorMessage = '';
    if (error.error instanceof ErrorEvent) {
      // Get client-side error
      errorMessage = error.error.message;
    } else {
      // Get server-side error
      errorMessage = `Error Code: ${error.status}\nMessage: ${error.message}`;
    }
    console.log(errorMessage);

    if (error.status == 401) {
      this.userService.signOut();
      this.router.navigate(['login']);
    }

    return throwError(errorMessage);
  }
}

interface ResponseApi {
  successfully: boolean
  reason: string
  data: any[]
}
