import { Component, Directive, EventEmitter, Input, Output, QueryList, ViewChildren, OnInit } from '@angular/core';
import { BusinessQuoteService } from '../services/business-quote.service';
import { NgbModal, NgbActiveModal, NgbModalConfig } from '@ng-bootstrap/ng-bootstrap';
import { FormControl } from '@angular/forms';
import { BusinessQuoteModalComponent } from '../business-quote-modal/business-quote-modal.component';

interface BusinessQuote {
  idBusinessQuote: number;
  idUser: number;
  insuredName: string;
  executiveName: string;
  supervisorName: string;
  createDate: Date;
  dateOfIssue: Date;
  updateDate: Date;
  state: string;
}

export type SortColumn = keyof BusinessQuote | '';
export type SortDirection = 'asc' | 'desc' | '';
const rotate: { [key: string]: SortDirection } = { 'asc': 'desc', 'desc': '', '': 'asc' };

const compare = (v1: string | number, v2: string | number) => v1 < v2 ? -1 : v1 > v2 ? 1 : 0;

export interface SortEvent {
  column: SortColumn;
  direction: SortDirection;
}

@Directive({
  selector: 'th[sortable]',
  host: {
    '[class.asc]': 'direction === "asc"',
    '[class.desc]': 'direction === "desc"',
    '(click)': 'rotate()'
  }
})
export class NgbdSortableBQQHeader {

  @Input() sortable: SortColumn = '';
  @Input() direction: SortDirection = '';
  @Output() sort = new EventEmitter<SortEvent>();

  rotate() {
    this.direction = rotate[this.direction];
    this.sort.emit({ column: this.sortable, direction: this.direction });
  }
}

@Component({
  selector: 'app-business-quote-query',
  templateUrl: './business-quote-query.component.html',
  styleUrls: ['./business-quote-query.component.css']
})
export class BusinessQuoteQueryComponent implements OnInit {
  filter = new FormControl('');
  search = new FormControl('');
  public bQuoteRequested;
  public bQuoteRequestedFilter;
  page = 1;
  pageSize = 10;
  collectionSize;

  @ViewChildren(NgbdSortableBQQHeader) headers: QueryList<NgbdSortableBQQHeader>;

  constructor(
    public bQuoteService: BusinessQuoteService,
    private modalBusinessQuote: NgbModal) { }

  ngOnInit() {
    this.loadData();
    this.filter.valueChanges.subscribe(value => {
      this.bQuoteRequestedFilter = this.findByFilter(value);
    });
    this.search.valueChanges.subscribe(value => {
      if (value.length == 0) {
        this.loadData();
      }
    });
  }

  loadData() {
    this.filter.setValue('');
    this.bQuoteService.getBusinessQuoteListByFilter('').then(x => {
      this.bQuoteRequested = x.data;
      this.bQuoteRequestedFilter = x.data;
      this.collectionSize = x.data.length;
      this.refreshQuotes();
    });
  }

  searchByFilter() {
    this.bQuoteService.getBusinessQuoteListByFilter(this.search.value).then(x => {
      this.bQuoteRequested = x.data;
      this.bQuoteRequestedFilter = x.data;
      this.collectionSize = x.data.length;
      this.refreshQuotes();
    });
  }

  findByFilter(text: string): any[] {
    return this.bQuoteRequested.filter(bQuote => {
      const term = text.toLowerCase();
      if (bQuote.supervisorName == null) {
        return bQuote.insuredName.toLowerCase().includes(term)
          || bQuote.executiveName.toLowerCase().includes(term)
          || bQuote.state.toLowerCase().includes(term);
      }else {
        return bQuote.insuredName.toLowerCase().includes(term)
          || bQuote.executiveName.toLowerCase().includes(term)
          || bQuote.supervisorName.toLowerCase().includes(term)
          || bQuote.state.toLowerCase().includes(term);
      }
    }).slice((this.page - 1) * this.pageSize, (this.page - 1) * this.pageSize + this.pageSize);
  }

  refreshQuotes() {
    this.bQuoteRequestedFilter = this.bQuoteRequested
      .slice((this.page - 1) * this.pageSize, (this.page - 1) * this.pageSize + this.pageSize);
  }

  onSort({ column, direction }: SortEvent) {

    // resetting other headers
    this.headers.forEach(header => {
      if (header.sortable !== column) {
        header.direction = '';
      }
    });

    // sorting
    if (direction === '' || column === '') {
      this.refreshQuotes();
    } else {
      this.bQuoteRequestedFilter = this.bQuoteRequested
        .slice((this.page - 1) * this.pageSize, (this.page - 1) * this.pageSize + this.pageSize).sort((a, b) => {
          const res = compare(a[column], b[column]);
          return direction === 'asc' ? res : -res;
        });
    }
  }

  Edit(idBusinessQuote, idExecutive, readOnly) {
    const modalRef = this.modalBusinessQuote.open(BusinessQuoteModalComponent, { size: 'lg', backdrop: 'static' });
    modalRef.componentInstance.isModal = true;
    modalRef.componentInstance.idBusinessQuote = idBusinessQuote;
    modalRef.componentInstance.idExecutive = idExecutive;
    modalRef.componentInstance.readOnly = readOnly;
    modalRef.result.then(() => { this.loadData(); }).catch(x => { });
  }

}
