import { Component, OnInit, Injectable, Input } from '@angular/core';
import { NgbDateParserFormatter, NgbDateStruct, NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { CustomDateParserFormatter } from '../helper/custom-date-parser-formatter';
import { InsuredService } from '../services/insured.service';
import { TypeShipmentService } from '../services/type-shipment.service';
import { CountryService } from '../services/country.service';
import { CategoryGoodService } from '../services/category-good.service';
import { ShippingMethodService } from '../services/shipping-method.service';
import { CurrencyService } from '../services/currency.service';
import { InsuranceTypeService } from '../services/insurance-type.service';
import { QuoteService } from '../services/quote.service';
import { AvailableCoverageService } from '../services/available-coverage.service';
import { Options } from 'select2';
import { FormBuilder, FormGroup, Validators, FormArray } from '@angular/forms';
import { ActivatedRoute } from "@angular/router";
import { Router } from "@angular/router";
import { DecimalPipe } from '@angular/common';
import { UserService } from '../services/user.service';


@Component({
  selector: 'ngbd-modal-content',
  template: `
    <div class="modal-body">
      <app-insured [isConsignee]="isConsignee" [isCompany]="isCompany"></app-insured>
    </div>
  `
})
export class NgbdModalContent {
  @Input() isConsignee;
  @Input() isCompany;

  constructor(public activeModal: NgbActiveModal) { }
}

@Component({
  selector: 'app-quote',
  templateUrl: './quote.component.html',
  styleUrls: ['./quote.component.css'],
  providers: [
    { provide: NgbDateParserFormatter, useClass: CustomDateParserFormatter },
    DecimalPipe
  ]
})
export class QuoteComponent implements OnInit {
  public insuredList: any = [];
  public insuredListInput: any = [];
  public consigneeList: any = [];
  public consigneeListInput: any = [];
  public typeShipmentList: any[];
  public countries: any[];
  public categoryGood: any = [];
  public shipmentMethodList: any[];
  public currencies: any = [];
  public insuranceTypes: any[];
  public availableCovers: any[];
  public optionsSelect: Options;
  public insuredInfo;
  public consigneeInfo;
  public dateIni: NgbDateStruct;
  public idQuickQuote: string;
  public invoiceValue;
  public currentUser: any;
  public idClient = 1;
  public currentSymbol: string;
  insuredForm: FormGroup;
  shippingForm: FormGroup;
  cargoForm: FormGroup;
  submittedInsured = false;
  submittedShipping = false;
  submittedCargo = false;
  active;


  constructor(
    public insuredService: InsuredService,
    public typeShipmentService: TypeShipmentService,
    public countryService: CountryService,
    public categoryService: CategoryGoodService,
    public shippingMethodService: ShippingMethodService,
    public currencyService: CurrencyService,
    public insuranceTypeService: InsuranceTypeService,
    public quoteService: QuoteService,
    public availableService: AvailableCoverageService,
    private formBuilder: FormBuilder,
    private route: ActivatedRoute,
    private router: Router,
    private modalService: NgbModal,
    private _decimalPipe: DecimalPipe,
    private userService: UserService
  ) { }

  ngOnInit() {
    this.idQuickQuote = this.route.snapshot.paramMap.get("quickQuoteId");
    let today = new Date();
    this.dateIni = {
      year: today.getFullYear(),
      month: today.getMonth() + 1,
      day: today.getDate()
    };

    this.insuredForm = this.formBuilder.group({
      lossPayee: ''
    });

    this.shippingForm = this.formBuilder.group({
      typeShipment: ['', Validators.required],
      shippingDate: ['', Validators.required],
      countryShipCommence: ['', Validators.required],
      countryDestination: ['', Validators.required]
    });
    /*
     * vesselName: ['', Validators.required],
     * addicionalCarrier: '',
     * placeShipCommence: ['', Validators.required],
     * countryPortLoading: ['', Validators.required],
     * portLoading: '',
     * finalDestination: ['', Validators.required],
     * countryPortDischarge: ['', Validators.required],
     * portDischarge: ''
     * */

    this.cargoForm = this.formBuilder.group({
      categoryGood: ['', Validators.required],
      descriptionGood: '',
      shippingMethod: ['', Validators.required],
      currency: ['', Validators.required],
      merchandiseValue: ['', Validators.required],
      freightValue: ['', Validators.required],
      insuranceLimit: ['', Validators.required],
      available: ['', Validators.required]
    });
    /*
     * markNumber: '',
     * pieceWeight: '',
     * addicionalComment: ''
     * */

    this.categoryService.getCategoryGood()
      .then(x => {
        this.categoryGood = [{ id: '', text: '' }];
        x.data.forEach(category => {
          this.categoryGood.push({ id: category.idCategoryGood, text: category.description });
        });
      });

    /*this.insuredService.getInsuredList(false)
      .then(x => {
        this.insuredList = x.data;
        this.insuredListInput = [{ id: '', text: '' }];
        x.data.forEach(insured => {
          this.insuredListInput.push({ id: insured.idInsured, text: insured.name });
        });
      });

    this.insuredService.getInsuredList(true)
      .then(x => {
        this.consigneeList = x.data;
        this.consigneeListInput = [{ id: '', text: '' }];
        x.data.forEach(insured => {
          this.consigneeListInput.push({ id: insured.idInsured, text: insured.name });
        });
      });*/

    this.typeShipmentService.getTypeShipmentList()
      .then(x => this.typeShipmentList = x.data);

    this.countryService.getCountries()
      .then(x => this.countries = x.data);

    this.shippingMethodService.getShippingMethodList()
      .then(x => this.shipmentMethodList = x.data);

    this.currencyService.getCurrencies()
      .then(x => this.currencies = x.data);

    this.insuranceTypeService.getInsuranceType()
      .then(x => this.insuranceTypes = x.data);

    this.availableService.getAvailableCoverage()
      .then(x => this.availableCovers = x.data);

    /*(this.insuredForm as unknown as FormArray).valueChanges.subscribe(values => {
      if (values.insured != undefined && values.insured != "") {
        this.insuredInfo = this.insuredList.find(x => x.idInsured == values.insured);
      }
    });*/

    (this.cargoForm as unknown as FormArray).valueChanges.subscribe(values => {
      let resultCalculate = 0;
      let merchanValue = values.merchandiseValue != '' ? parseFloat(values.merchandiseValue.replace(',', '')) : 0;
      let freightValue = values.freightValue != '' ? parseFloat(values.freightValue.replace(',', '')) : 0;
      if (values.insuranceLimit != undefined && values.insuranceLimit != "" && this.insuranceTypes != null) {
        let insurance = this.insuranceTypes.find(x => x.idInsuranceType == values.insuranceLimit)
        resultCalculate = ((insurance.insuredPorcent / 100) * (merchanValue + freightValue));
      } else {
        resultCalculate = merchanValue + freightValue;
      }
      this.invoiceValue = this._decimalPipe.transform(resultCalculate, "1.2-2")
    });

    if (this.idQuickQuote !== null) {
      this.getQuickQuoteInfo(this.idQuickQuote);
    }

    this.optionsSelect = {
      placeholder: "Select option...",
      allowClear: true,
      width: "100%"
    };

    this.currentUser = this.userService.getCurrentUser();
    if (this.currentUser != null) {
      this.idClient = this.currentUser.localUser.idClient;
    } else {
      this.router.navigate(['/']);
    }

  }

  // convenience getter for easy access to form fields
  get fIn() { return this.insuredForm.controls; }
  get fInsuredValid() { return this.insuredForm.valid; }
  get fSh() { return this.shippingForm.controls; }
  get fShippingValid() { return this.shippingForm.valid; }
  get fCar() { return this.cargoForm.controls; }
  get fCargoValid() { return this.cargoForm.valid; }

  getQuickQuoteInfo(guidQuickQuote) {
    this.quoteService.getQuickQuoteInfo(guidQuickQuote)
      .then(x => {
        if (x.data != null) {
          let quickQuote = JSON.parse(x.data.content);
          this.cargoForm.setValue({
            categoryGood: quickQuote.IdCategoryGood,
            currency: quickQuote.IdCurrency,
            merchandiseValue: this._decimalPipe.transform(quickQuote.MerchandiseValue.toString().replace(',', ''), "1.2-2"),
            freightValue: this._decimalPipe.transform(quickQuote.FreightValue.toString().replace(',', ''), "1.2-2"),
            insuranceLimit: quickQuote.IdInsuranceType,
            available: quickQuote.IdAvailableCoverage,
            descriptionGood: '',
            shippingMethod: quickQuote.IdShippingMethod
          });

          this.currentSymbol = this.currencies.find(x => x.idCurrency == quickQuote.IdCurrency).symbol;
        }
      });
  }

  onSubmitCargo() {
    this.submittedCargo = true;

    if (this.cargoForm.invalid) {
      return;
    }

    this.quoteService.createQuote(this.insuredForm.value, this.shippingForm.value, this.cargoForm.value, this.idClient, this.insuredInfo.idInsured, this.consigneeInfo ? this.consigneeInfo.idInsured : null).then(x => {
      if (x.data != null) {
        this.router.navigate(['/reviewquote', x.data.guidQuote]).then((e) => {
          if (e) {
            //console.log("Navigation is successful!");
          } else {
            //console.log("Navigation has failed!");
          }
        });
      }
    });

  }

  callInsured(isConsignee) {
    const modalRef = this.modalService.open(NgbdModalContent);
    modalRef.componentInstance.isConsignee = isConsignee;
    modalRef.componentInstance.isCompany = false;
    modalRef.result.then((result) => {
      if (isConsignee) {
        this.consigneeInfo = result;
      } else {
        this.insuredInfo = result;
      }
    }).catch(x => { });
  }

  updateCurrentSymbol(event: any) {
    if (event.target.value != undefined && event.target.value != '')
      this.currentSymbol = this.currencies.find(x => x.idCurrency == event.target.value).symbol;
  }

  /*reloadInsured() {
    this.insuredService.getInsuredList(false)
      .then(x => {
        this.insuredList = x.data;
        this.insuredListInput = [{ id: '', text: '' }];
        x.data.forEach(insured => {
          this.insuredListInput.push({ id: insured.idInsured, text: insured.name });
        });
      });

    this.insuredService.getInsuredList(true)
      .then(x => {
        this.consigneeList = x.data;
        this.consigneeListInput = [{ id: '', text: '' }];
        x.data.forEach(insured => {
          this.consigneeListInput.push({ id: insured.idInsured, text: insured.name });
        });
      });
  }*/

  onReset() {
    this.active = 1;
    this.submittedInsured = false;
    this.submittedShipping = false;
    this.submittedCargo = false;
    this.insuredInfo = null;
    this.insuredForm.reset();
    this.shippingForm.reset();
    this.cargoForm.reset();
  }

}
