import { Component, OnInit, Input, ElementRef  } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormArray } from '@angular/forms';
import { CountryService } from '../services/country.service';
import { InsuredService } from '../services/insured.service';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-insured',
  templateUrl: './insured.component.html',
  styleUrls: ['./insured.component.css']
})
export class InsuredComponent implements OnInit {
  public countries: any[];
  insuredForm: FormGroup;
  submitted = false;
  action = "Save";
  dataResult: any;
  @Input() isConsignee;
  @Input() isCompany;

  constructor(
    public countryService: CountryService,
    public insuredService: InsuredService,
    private formBuilder: FormBuilder,
    public activeModal: NgbActiveModal,
    private el: ElementRef
  ) { }

  ngOnInit() {

    this.insuredForm = this.formBuilder.group({
      name: ['', Validators.required],
      country: ['', Validators.required],
      city: ['', Validators.required],
      address: ['', Validators.required],
      phone: ['', Validators.required],
      email: ['', Validators.required],
      contactPerson: this.isCompany ? ['', Validators.required] : [''],
      taxId: [''],
      creditLimit: ['']
    });

    debugger;
    const invalidControl = this.el.nativeElement.querySelector('.emailText');

    if (invalidControl) {
      invalidControl.focus();
    }

    this.countryService.getCountries()
      .then(x => this.countries = x.data);
  }

  // convenience getter for easy access to form fields
  get f() { return this.insuredForm.controls; }

  close() {
    this.activeModal.close(null);
  }
  onSubmit() {
    debugger;
    this.submitted = true;

    if (this.insuredForm.invalid) {
      return;
    }

    if (this.action == 'Save') {
      this.insuredService.createInsured(this.insuredForm.value, this.isConsignee, this.isCompany, 0).then(x => {
        if (x.successfully) {
          this.dataResult = x.data;
          this.activeModal.close(this.dataResult);
        }
      });
    } else {
      this.activeModal.close(this.dataResult);
    }
  }


  findByEmail() {
    let email = this.insuredForm.get("email").value;
    if (email != '') {
      if (this.isCompany) {
        this.insuredService.getCompanyByEmail(email).then(res => {
          if (res.successfully && res.data != null) 
            this.setField(res.data);
          else 
            this.setEmptyField();
        });
      } else {
        this.insuredService.getInsuredByEmail(email).then(res => {
          if (res.successfully && res.data != null) 
            this.setField(res.data);
          else 
            this.setEmptyField();
        });
      }
    }
  }

  setEmptyField() {
    let email = this.insuredForm.get("email").value;
    this.action = 'Save';
    this.insuredForm.setValue({
      name: '',
      country: '',
      city: '',
      address: '',
      phone: '',
      contactPerson: '',
      taxId: '',
      email: email,
      creditLimit: 0
    });
  }

  setField(data) {
    this.dataResult = data;
    this.action = 'Select';
    this.insuredForm.setValue({
      name: this.dataResult.name,
      country: this.dataResult.idCountry,
      city: this.dataResult.city,
      address: this.dataResult.address,
      phone: this.dataResult.phone,
      email: this.dataResult.email,
      contactPerson: this.dataResult.contactPerson,
      taxId: this.dataResult.taxId,
      creditLimit: this.dataResult.creditLimit
    });
  }

  onReset() {
    this.submitted = false;
    this.insuredForm.reset();
  }
}
