import { Component, OnInit, Input } from '@angular/core';
import { PremiumSetupService } from '../services/premium-setup.service';
import { NgbModal, NgbActiveModal, NgbModalConfig } from '@ng-bootstrap/ng-bootstrap';
import { ModalConfirmationComponent } from '../modal-confirmation/modal-confirmation.component';
import { FormGroup, FormBuilder, FormControl } from '@angular/forms';
import { Router } from '@angular/router';
import { UserService } from '../services/user.service';
import { startWith, map } from 'rxjs/operators';

@Component({
  selector: 'validation-modal-content',
  template: `
    <div class="modal-body">
      <form [formGroup]="validationForm">
        <div class="form-group">
          <label for="pass">Enter Password</label>
          <input type="password" class="form-control" id="pass" formControlName="pass" />
        </div>
      </form>
    </div>
    <div class="modal-footer">
      <button type="button" class="btn btn-outline-dark" (click)="activeModal.close(f)">Send</button>
    </div>
  `
})
export class ValidationModalContent {
  validationForm: FormGroup;
  constructor(config: NgbModalConfig,
    public activeModal: NgbActiveModal,
    private formBuilder: FormBuilder) {
    config.backdrop = 'static';
    config.keyboard = false;
  }
  ngOnInit() {
    this.validationForm = this.formBuilder.group({
      pass: ['']
    });
  }
  get f() { return this.validationForm.get("pass").value; }
}

@Component({
  selector: 'premium-modal-content',
  template: `
    <div class="modal-body">
      <app-premium-setup [isModal]="isModal" [itemEdit]="itemEdit" [idExecutive]="idExecutive" [premiumActiveModal]="activeModal"></app-premium-setup>
    </div>
    <div class="modal-footer">
      <button type="button" class="btn btn-outline-dark" (click)="activeModal.close('Close click')">Close</button>
    </div>
  `
})
export class PremiumModalContent {
  @Input() isModal: boolean;
  @Input() itemEdit: string;
  @Input() idExecutive: number;
  constructor(public activeModal: NgbActiveModal) { }
}

@Component({
  selector: 'app-premium-setup-list',
  templateUrl: './premium-setup-list.component.html',
  styleUrls: ['./premium-setup-list.component.css']
})
export class PremiumSetupListComponent implements OnInit {
  public premiumCommission;
  public premiumCommissionFilter;
  public itemAction = [];
  filter = new FormControl('');
  executive = new FormControl('');
  public executives: any[];
  public isEmptyExecutive = false;

  constructor(
    public premiumSetupService: PremiumSetupService,
    private modalService: NgbModal,
    private modalPremium: NgbModal,
    public userService: UserService,
    private modalValidation: NgbModal,
    private router: Router
  ){}

  ngOnInit() {
    //const modalRef = this.modalValidation.open(ValidationModalContent);
    //modalRef.result.then((result) => {
    //  if (result != "abc123456") {
    //    this.router.navigate(['/']);
    //  }
    //}).catch(x => { });
    this.loadData(true);
    this.filter.valueChanges.subscribe(value => {
      this.premiumCommissionFilter = this.search(value);
    });
    this.userService.getExecutiveList().then(x => {
      this.executives = x.data;
    });
  }

  handleSelected(e) {
    debugger;
    if (e.target.checked === true) {
      this.itemAction.push(e.target.value);
    } else {
      this.itemAction.splice(this.itemAction.indexOf(e.target.value), 1);
    }
  }

  loadData(isFirst: boolean) {
    debugger;
    this.filter.setValue('');
    if (this.executive.value != '') {
      this.premiumSetupService.GetPremiumCommissionList(+this.executive.value).then(x => {
        this.premiumCommission = x.data;
        this.premiumCommissionFilter = x.data;
      });
    } else {
      if (!isFirst)
        this.isEmptyExecutive = true;
    }
    this.itemAction = [];
  }

  delete() {
    if (this.executive.value != '') {
      const modalRef = this.modalService.open(ModalConfirmationComponent);
      modalRef.componentInstance.title = "Delete Record";
      modalRef.componentInstance.message = "Are you sure you want to delete(s) the selected item(s)?";
      modalRef.result.then((result) => {
        if (result) {
          this.premiumSetupService.DeletePremiumCommissionList(this.itemAction).then(x => {
            if (x.successfully) {
              this.loadData(false);
            }
          });
        }
      }).catch(x => { });
    } else {
      this.isEmptyExecutive = true;
    }
  }

  newPremium() {
    if (this.executive.value != '') {
      const modalRef = this.modalPremium.open(PremiumModalContent);
      modalRef.componentInstance.isModal = true;
      modalRef.componentInstance.idExecutive = +this.executive.value;
      modalRef.result.then(() => { this.loadData(false); }).catch(x => { });
    } else {
      this.isEmptyExecutive = true;
    }
  }

  editPremium() {
    debugger;
    if (this.executive.value != '') {
      const modalRef = this.modalPremium.open(PremiumModalContent);
      modalRef.componentInstance.isModal = true;
      modalRef.componentInstance.itemEdit = this.itemAction[0];
      modalRef.componentInstance.idExecutive = +this.executive.value;
      modalRef.result.then(() => { this.loadData(false); }).catch(x => { });
    }
    else {
      this.isEmptyExecutive = true;
    }
  }

  search(text: string): any[] {
    if (this.premiumCommission != undefined && this.premiumCommission != null) {
      return this.premiumCommission.filter(premium => {
        const term = text.toLowerCase();
        return premium.providerName.toLowerCase().includes(term)
          || premium.availableCoverageDescription.toLowerCase().includes(term)
          || premium.categoryGoodDescription.toLowerCase().includes(term)
          || premium.clientName.toLowerCase().includes(term)
          || premium.commissionName.toLowerCase().includes(term);
      });
    }
  }

  onChange(data) {
    if (this.executive.value != '') {
      this.isEmptyExecutive = false;
    }
  }
}
