import { Injectable } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { QuestionGroupBase } from './question-group-base';
import { QuestionBase } from './question-base';

@Injectable()
export class QuestionControlService {
  constructor() { }

  toFormGroup(questions: QuestionBase<string>[]) {
    const group: any = {};

    questions.forEach(question => {
      group[question.key] = question.required ? new FormControl(question.value || '', Validators.required)
        : new FormControl(question.value || '');
    });
    return new FormGroup(group);
  }

  GrpToFormGroup(questionsGroup: QuestionGroupBase[]) {
    const group: any = {};

    if (!!questionsGroup && questionsGroup.length > 0) {
      questionsGroup.forEach(groupForm => {

        groupForm.questions.forEach(question => {

          var initialvalue = '';
          if (question.controlType == 'datetime') initialvalue = null;

          group[question.key] = question.required ? new FormControl(question.value || initialvalue, Validators.required)
            : new FormControl(question.value || initialvalue);

        })

      });
    }
    //Fijos
    //group["insuredTaxId"] = new FormControl('', Validators.required);
    //group["insuredZipCode"] = new FormControl('', Validators.required);
    group["infoTypeShipment"] = new FormControl('', Validators.required);
    group["infoShippingDate"] = new FormControl('', Validators.required);
    group["infoDescriptionGood"] = new FormControl('');
    group["infoCarrier"] = new FormControl('', Validators.required);
    group["infoDocumentNumber"] = new FormControl('', Validators.required);
    group["infoObservation"] = new FormControl('');
    group["infoEstimatedDepartureDate"] = new FormControl('', Validators.required);
    group["infoEstimatedArrivalDate"] = new FormControl('', Validators.required);
    
    
    group["billingOption"] = new FormControl('insured');

    group["merchandiseValue"] = new FormControl('');
    group["insuredValue"] = new FormControl('');
    group["valueOriginExpense"] = new FormControl('');
    group["freightValue"] = new FormControl('');
    group["otherValue"] = new FormControl('');
    group["cptValue"] = new FormControl('');
    group["trmValue"] = new FormControl('');
    group["cptColValue"] = new FormControl('');
    group["tariffCptValue"] = new FormControl('');
    group["taxCptValue"] = new FormControl('');
    group["tributesCptValue"] = new FormControl('');
    group["remainingValue"] = new FormControl('');

    group["inPremiumSale"] = new FormControl('');
    group["premiumSale"] = new FormControl('');

    group["inDatePast"] = new FormControl('');
    

    return new FormGroup(group);
  }
}
