import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from "@angular/router";
import { Router } from "@angular/router";
import { each } from 'jquery';
import { QuoteService } from '../services/quote.service';
import { FormBuilder, FormControl, FormGroup, Validators, FormArray } from '@angular/forms';
import { Options } from 'select2';
import { QuestionService } from '../dynamic-form/lib/question.service';
import { QuestionGroupBase } from '../dynamic-form/lib/question-group-base';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-quote-certificate-validate',
  templateUrl: './quote-certificate-validate.component.html',
  styleUrls: ['./quote-certificate-validate.component.css'],
  providers: [QuestionService]
})
export class QuoteCertificateValidateComponent implements OnInit {
  public idQuote: string;
  public loading: boolean = false;
  public submitted: boolean = false;
  public trySubmit: boolean = false;
  questionsGroups$: Observable<QuestionGroupBase[]>;
  loadedQuestion: boolean;
  form: FormGroup;
  generateCertificateForm: FormGroup;

  constructor(
    private route: ActivatedRoute,
    public quoteService: QuoteService,
    private questionService: QuestionService,
    private router: Router,
    private formBuilder: FormBuilder,
  ) { }

  get isValidCertificateValue() { return this.form.controls["certificateValue"].valid; }

  ngOnInit() {

    const group: any = {};
    group["certificateValue"] = new FormControl('', Validators.required);
    this.form = new FormGroup(group);
    if (!!this.route.snapshot.paramMap.get("cert")) {
      this.form.controls["certificateValue"].setValue(this.route.snapshot.paramMap.get("cert"));
      this.onSubmit();
    }

  }

  onSubmit() {
    this.trySubmit = true;
    if (this.form.valid) {
      this.loading = true;
      this.submitted = false;
      debugger;
      this.quoteService.ValidateCertificate(this.form.controls["certificateValue"].value)
        .then(x => {
          this.loading = false;
          this.submitted = true;
          if (x.successfully == true) {

            this.questionService.getQuestionsGroup(x.data.quote.guidQuote).then(y => {
              this.idQuote = x.data.quote.guidQuote;
              this.questionsGroups$ = y;
              this.loadedQuestion = true;
            });
            
          } else {
            this.idQuote = null;
          }
        });
    }
  }
}
