import { Component, OnInit, Input } from '@angular/core';
import { Validators, FormGroup, FormBuilder } from '@angular/forms';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ModalConfirmationComponent } from '../modal-confirmation/modal-confirmation.component'
import { UserService } from '../services/user.service';

@Component({
  selector: 'app-supervisors-administration',
  templateUrl: './supervisors-administration.component.html',
  styleUrls: ['./supervisors-administration.component.css']
})
export class SupervisorsAdministrationComponent implements OnInit {
  @Input() idUserFilter: number = null;
  submitted = false;
  public userSupervisorList: any;
  public message: string;
  public supervisors: any[];
  public executives: any[];
  userSupervisorForm: FormGroup;

  constructor(
    private modalConfirmation: NgbModal,
    public userService: UserService,
    private formBuilder: FormBuilder
  ) { }

  ngOnInit() {
    this.userSupervisorForm = this.formBuilder.group({
      supervisor: !!this.idUserFilter ? [''] : ['', Validators.required],
      executive: ['', Validators.required]
    });

    this.userService.getSupervisorList().then(x => {
      this.supervisors = x.data;
    });

    this.userService.getExecutiveList().then(x => {
      this.executives = x.data;
    });
    this.loadData();
  }

  loadData() {
    this.userService.getUserSupervisorList(this.idUserFilter).then(x => {
      this.userSupervisorList = x.data;
    });
  }

  onSubmit() {
    debugger;
    if (!!this.idUserFilter) this.userSupervisorForm.value.supervisor = this.idUserFilter.toString();

    this.submitted = true;
    if (this.userSupervisorForm.invalid)
      return;
    this.submitted = false;
    this.userService.createUserSupervisor(this.userSupervisorForm.value).then(x => {
      if (x.successfully) {
        this.loadData();
      } else {
        this.message = x.reason;
      }
    });
  }

  close() {
    this.message = null;
  }

  get f() { return this.userSupervisorForm.controls; }

  Delete(idUser, idSupervisor) {
    const modalRef = this.modalConfirmation.open(ModalConfirmationComponent);
    modalRef.componentInstance.title = "Delete Record";
    modalRef.componentInstance.message = "Are you sure you want to delete the selected item?";
    modalRef.result.then((result) => {
      if (result) {
        this.userService.deleteUserSupervisor(idUser, idSupervisor)
          .then(x => {
            if (x.successfully) {
              this.loadData();
            } else {
              this.message = x.reason;
            }
          });
      }
    }).catch(x => { });
  }
}
