import { Component, OnInit } from '@angular/core';
import { UserService } from '../services/user.service';
import { AuthService } from "angularx-social-login";
import { Router } from "@angular/router";

@Component({
  selector: 'app-nav-menu',
  templateUrl: './nav-menu.component.html',
  styleUrls: ['./nav-menu.component.css']
})
export class NavMenuComponent implements OnInit {
  isExpanded = false;
  public loggedIn: boolean;
  private localUser: any;
  private nameUser: string;
  private imageUser: string;
  private currentUser: string;
  public userRole: string;

  constructor(
    private userService: UserService,
    private authService: AuthService,
    private router: Router
  ) { }

  ngOnInit() {
    this.currentUser = this.userService.getCurrentUser();

    this.authService.authState.subscribe((user) => {
      if (user != null) {
        this.userService.getUserByEmail(user).then((response) => {
          if (response.data != null) {
            this.localUser = { socialUser: user, localUser: response.data };
          } else {
            this.localUser = { socialUser: user, localUser: { idClient: 1 } };
          }
          this.verifyUserRole();
          this.userService.setUser(this.localUser);
        });
      }
      this.loggedIn = (user != null || this.currentUser != null);
    });

    this.userService.authState().subscribe((user) => {
      if (user != null) {
        if (user.socialUser != null) {
          this.nameUser = user.socialUser.name;
          this.imageUser = user.socialUser.photoUrl;
        }
        else {
          this.nameUser = user.localUser.name;
          this.currentUser = user;
        }
      }
      this.localUser = user;
      this.verifyUserRole();
      this.loggedIn = (user != null || this.currentUser != null);
    });

    if (this.currentUser != null) {
      this.userService.setUser(this.currentUser);
      this.localUser = this.currentUser;
      this.loggedIn = (this.currentUser != null);
    }
  }

  logOut() {
    this.nameUser = null;
    this.imageUser = null;
    this.currentUser = null;
    this.userService.signOut();
    this.router.navigate(['/']).then((e) => {});
  }

  collapse() {
    this.isExpanded = false;
  }

  toggle() {
    this.isExpanded = !this.isExpanded;
  }

  verifyUserRole() {
    if (this.localUser != null) {
      if (this.localUser.localUser != null && this.localUser.localUser.userRoles != null) {
        this.userRole = "";
        this.userRole += this.localUser.localUser.userRoles.map(x => {
            return `${x.role.roleDescription}-`;
        });
      } else {
        this.userRole = "";
      }
    }
  }

}
