import { Component, OnInit } from '@angular/core';
import { SettingService } from '../services/setting.service';
import { FormControl, Validators, FormGroup, FormBuilder } from '@angular/forms';

@Component({
  selector: 'app-settings',
  templateUrl: './settings.component.html',
  styleUrls: ['./settings.component.css']
})
export class SettingsComponent implements OnInit {
  ccoEmail = new FormControl('');
  quotationValidityDate = new FormControl('');
  premiumMinimum = new FormControl('');
  limitShipmentWithoutAuthorization = new FormControl('');

  constructor(
    public settingService: SettingService) { }

  ngOnInit() {
    let result: any;
    this.settingService.getOptionSetting().then(x => {
      result = x.data;
      if (result != null) {
        this.ccoEmail.setValue(result.emailCC);
        this.quotationValidityDate.setValue(result.quotationValidityDate);
        this.premiumMinimum.setValue(result.premiumMinimum);
        this.limitShipmentWithoutAuthorization.setValue(result.limitShipmentWithoutAuthorization);
      }
    });
  }

  onSubmit() {

    let options = {
      "emailCC": !!this.ccoEmail.value ? this.ccoEmail.value: '',
      "quotationValidityDate": !!this.quotationValidityDate.value ? this.quotationValidityDate.value : 0,
      "premiumMinimum": !!this.premiumMinimum.value ? this.premiumMinimum.value : 0,
      "limitShipmentWithoutAuthorization": !!this.limitShipmentWithoutAuthorization.value ? this.limitShipmentWithoutAuthorization.value : 0,
    }

    this.settingService.updateOptionSetting(options).then(x => {
      if (!x.successfully)
        console.log(x.reason);
    });
    
  }

}
