import { Injectable, Inject } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { retry, catchError } from 'rxjs/operators';
import { UserService } from '../../app/services/user.service';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class QuoteService {
  private baseApiUrl: string;

  constructor(
    private http: HttpClient, @Inject('BASE_API_URL') baseApiUrl: string,
    private userService: UserService,
    private router: Router
  ) {
    this.baseApiUrl = baseApiUrl;
  }

  //POST
  getPremiumCalculate(object: any, idClient: number): Promise<ResponseApi> {
    let quote = { "idCategoryGood": +object.categoryGood, "idAvailableCoverage": +object.available, "idCurrency": +object.currency, "merchandiseValue": parseFloat(object.merchandiseValue.replace(',', '')), "freightValue": parseFloat(object.freightValue.replace(',', '')), "idInsuranceType": +object.insuranceLimit, "idClient": +idClient };
    try {
      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}QuickQuote/QuotePremiumCalculation`, JSON.stringify(quote), this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  //POST
  createQuote(insuredForm: any, shippingForm: any, cargoForm: any, idClient: number, idInsured, idConsignee): Promise<ResponseApi> {
    let quote = this.buildJson(insuredForm, shippingForm, cargoForm, idClient, idInsured, idConsignee);
    try {
      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}Quote/CreateQuote`, JSON.stringify(quote), this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  //POST
  finishQuickQuote(object: any): Promise<ResponseApi> {
    let quote = { "idCategoryGood": +object.categoryGood, "idAvailableCoverage": +object.available, "idShippingMethod": +object.shippingMethod, "idCurrency": +object.currency, "merchandiseValue": this.removeFormatNumber(object.merchandiseValue), "freightValue": this.removeFormatNumber(object.freightValue), "idInsuranceType": +object.insuranceLimit };
    try {
      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}QuickQuote/CreateQuickQuote`, JSON.stringify(quote), this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  //POST
  createOrUpdateValuesForm(values): Promise<ResponseApi> {
    try {
      debugger;
      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}QuoteFormFieldValue/CreateOrUpdateValues`, values, this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  //GET
  async getQuickQuoteInfo(guidQuickQuote): Promise<ResponseApi> {
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}QuickQuote/GetQuickQuote?guidQuickQuote=${guidQuickQuote}`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //GET
  async getReviewQuoteInfo(guidQuote): Promise<ResponseApi> {
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}Quote/GetReviewQuote?guidQuote=${guidQuote}`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //POST
  async AcceptQuote(guidQuote: string, idProvider: number, sendMail:number): Promise<ResponseApi> {
    try {
      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}Quote/AcceptQuote?guidQuote=${guidQuote}&idProvider=${idProvider}&sendMail=${sendMail}`, {}, this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  //POST
  async PreviewQuote(guidQuote: string, idProvider: number): Promise<ResponseApi> {
    try {
      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}Quote/PreviewQuote?guidQuote=${guidQuote}&idProvider=${idProvider}`, {}, this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  //POST
  async GeneratePDFQuoteAccepted(guidQuote: string): Promise<ResponseApi> {
    try {
      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}Quote/GeneratePDFQuoteAccepted?guidQuote=${guidQuote}`, {}, this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  async GeneratePDFCertificate(guidQuote: string): Promise<ResponseApi> {
    try {
      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}Quote/GeneratePdfCertificate?guidQuote=${guidQuote}`, {}, this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  //POST
  async GenerateCertificate(values): Promise<ResponseApi> {
    try {
      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}Quote/GenerateCertificate`, values, this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  async SaveDraftCertificate(values): Promise<ResponseApi> {
    try {
      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}Quote/SaveDraftCertificate`, values, this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  //GET
  async ValidateCertificate(certificate): Promise<ResponseApi> {
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}QuoteCertificate/Validate?certificate=${certificate}`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //GET
  async getCertificateRequested(guidQuote): Promise<ResponseApi> {
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}Quote/GetCertificate?guidQuote=${guidQuote}`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //GET
  async getQuoteRequested(filter: string, idInsured: number = null, idCustomer: number = null): Promise<ResponseApi> {
    try {
      let idInsuredStr = '';
      if (!!idInsured) idInsuredStr = `&idInsured=${idInsured}`;

      let idCustomerStr = '';
      if (!!idCustomer) idCustomerStr = `&idCustomer=${idCustomer}`;

      let response = await this.http
        .get(`${this.baseApiUrl}Quote/GetQuoteByFilter?filter=${filter}` + idInsuredStr + idCustomerStr, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //GET
  async getQuoteById(guidQuote): Promise<ResponseApi> {
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}Quote/GetQuoteById?guidQuote=${guidQuote}`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //GET
  async getCateCoverProv(guidQuote): Promise<ResponseApi> {
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}Quote/getCateCoverProv?guidQuote=${guidQuote}`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }
  

  // Error handling
  errorHandl(error) {
    let errorMessage = '';
    if (error.error instanceof ErrorEvent) {
      // Get client-side error
      errorMessage = error.error.message;
    } else {
      // Get server-side error
      errorMessage = `Error Code: ${error.status}\nMessage: ${error.message}`;
    }
    console.log(errorMessage);

    if (error.status == 401) {
      this.userService.signOut();
      this.router.navigate(['login']);
    }

    return throwError(errorMessage);
  }

  buildJson(insuredForm, shippingForm, cargoForm, idClient, idInsured, idConsignee) {
    let shippingDate = this.buildDateShipping(shippingForm.shippingDate)
    return {
      "idCategoryGood": +cargoForm.categoryGood,
      "descriptionDetailsOfGood": cargoForm.descriptionGood,
      "idCurrency": +cargoForm.currency,
      "merchandiseValue": this.removeFormatNumber(cargoForm.merchandiseValue),
      "freightValue": this.removeFormatNumber(cargoForm.freightValue),
      "invoiceValue": cargoForm.invoiceValue,
      "increaseCountryValue": 0,
      "idInsuranceType": +cargoForm.insuranceLimit,
      "idClient": idClient,
      "idInsured": +idInsured,
      "idConsignee": +idConsignee,
      "lossPayee": insuredForm.lossPayee,
      "idTypeShipment": +shippingForm.typeShipment,
      "vesselName": shippingForm.vesselName,
      "additionalCarrierInformation": shippingForm.addicionalCarrier,
      "shippingDate": shippingDate,
      "placeShipmentCommences": shippingForm.placeShipCommence,
      "idCountryShipmentCommences": +shippingForm.countryShipCommence,
      "idCountryOfPortLoading": +shippingForm.countryPortLoading,
      "portOfLoading": shippingForm.portLoading,
      "finalDestinationOfShipment": shippingForm.finalDestination,
      "idCountryDestination": +shippingForm.countryDestination,
      "idCountryPortOfDischarge": +shippingForm.countryPortDischarge,
      "portOfDischarge": shippingForm.portDischarge,
      "idShippingMethod": +cargoForm.shippingMethod,
      "idAvailableCoverage": +cargoForm.available,
      "markAndNumber": cargoForm.markNumber,
      "piecesAndWeight": cargoForm.pieceWeight,
      "additionalComments": cargoForm.addicionalComment
    };
  }

  buildDateShipping(date) {
    return date.year + '-' + this.zfill(date.month, 2) + '-' + this.zfill(date.day, 2);
  }

  removeFormatNumber(value) {
    return parseFloat(value.toString().replace(',', ''));
  }

  zfill(number, width) {
    var numberOutput = Math.abs(number);
    var length = number.toString().length;
    var zero = "0";

    if (width <= length)
      return numberOutput.toString();
    else
      return ((zero.repeat(width - length)) + numberOutput.toString());
  }

}

interface ResponseApi {
  successfully: boolean
  reason: string
  data: any
  dataOptional: any
}

interface Quote {
  categoryGood: number
  currency: number
  merchandiseValue: number
  freightValue: number
  insuranceLimit: number
}
