import { Component, OnInit, Input } from '@angular/core';
import { Validators, FormGroup, FormBuilder } from '@angular/forms';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ModalConfirmationComponent } from '../modal-confirmation/modal-confirmation.component'
import { CustomerService } from '../services/customer.service';
import { UserService } from '../services/user.service';
import { Options } from 'select2';

@Component({
  selector: 'app-customer-executive',
  templateUrl: './customer-executive.component.html',
  styleUrls: ['./customer-executive.component.css']
})
export class CustomerExecutiveComponent implements OnInit {
  @Input() idCustomerFilter: number = null;
  submitted = false;
  public customerExecutiveList: any;
  public message: string;
  public companys: any[];
  public executives: any[];
  public executivesSelect2: any[];
  customerExecutiveForm: FormGroup;
  optionsSelect: Options;

  constructor(
    private modalConfirmation: NgbModal,
    public customerService: CustomerService,
    public userService: UserService,
    private formBuilder: FormBuilder
  ) { }

  ngOnInit() {
    this.customerExecutiveForm = this.formBuilder.group({
      customer: !!this.idCustomerFilter ? ['']: ['', Validators.required],
      executive: ['', Validators.required]
    });

    this.userService.getExecutiveList().then(x => {
      this.executives = x.data;
      debugger;
      this.executivesSelect2 = [];
      x.data.forEach(executive => {
        var text = '';
        if (!!executive.name) text = executive.name;
        if (!!executive.email) text += ' (' + executive.email + ')';
        this.executivesSelect2.push({ id: executive.idUser, text: text  });
      });

    });

    this.optionsSelect = {
      placeholder: "Select executive...",
      allowClear: false,
      width: "100%"
    };

    this.loadData();
  }

  loadData() {
    let idCustomer: number = null;
    if (!!this.idCustomerFilter) idCustomer = this.idCustomerFilter;
    this.customerService.getCustomerExecutiveList(idCustomer).then(x => {
      this.customerExecutiveList = x.data;
    });
  }

  onSubmit() {
    debugger;
    this.submitted = true;
    if (!!this.idCustomerFilter) this.customerExecutiveForm.value.customer = this.idCustomerFilter;

    if (this.customerExecutiveForm.invalid)
      return;
    this.submitted = false;
    this.customerService.createCustomerExecutive(this.customerExecutiveForm.value).then(x => {
      if (x.successfully) {
        this.loadData();
      } else {
        this.message = x.reason;
      }
    });
  }

  close() {
    this.message = null;
  }

  get f() { return this.customerExecutiveForm.controls; }

  Delete(idCustomer, idExecutive) {
    const modalRef = this.modalConfirmation.open(ModalConfirmationComponent);
    modalRef.componentInstance.title = "Delete Record";
    modalRef.componentInstance.message = "Are you sure you want to delete the selected item?";
    modalRef.result.then((result) => {
      if (result) {
        this.customerService.deleteCustomerExecutive(idCustomer, idExecutive)
          .then(x => {
            if (x.successfully) {
              this.loadData();
            } else {
              this.message = x.reason;
            }
          });
      }
    }).catch(x => { });
  }

}
