import { Component, OnInit } from '@angular/core';
import { Router } from "@angular/router";
import { UserService } from '../../services/user.service';
import { CustomerService } from '../../services/customer.service';
import { CountryService } from '../../services/country.service';
import { FormBuilder, FormGroup, Validators, FormControl, FormArray } from '@angular/forms';

@Component({
  selector: 'app-cus-profile',
  templateUrl: './cus-profile.component.html',
  styleUrls: ['./cus-profile.component.css']
})
export class CusProfileComponent implements OnInit {
  public titlePage: string = "My Customer Profile";
  public loading: boolean = true;
  public submittedNew: boolean = false;
  
  public idCustomer: number;
  public customerEdit: any = null;
  public countries: any[];

  customerUpdateForm: FormGroup;

  constructor(
    private userService: UserService,
    private customerService: CustomerService,
    private router: Router,
    private formBuilder: FormBuilder,
    public countryService: CountryService,
  ) { }

  ngOnInit() {

    this.customerUpdateForm = this.formBuilder.group({
      name: ['', Validators.required],
      country: ['', Validators.required],
      city: ['', Validators.required],
      address: ['', Validators.required],
      phone: ['', Validators.required],
      email: ['', Validators.required],
      taxId: [''],
      contactPerson: [''],
    });

    this.countryService.getCountries()
      .then(x => {
        this.countries = x.data
      });

    this.loading = true;
    debugger;
    var us = this.userService.getMyProfile().then(x => {
      if (!x.data.isCustomerUser) {
        this.router.navigate(['/']);
      } else {
        this.idCustomer = x.data.idCustomer;
        this.loading = false;

        this.customerService.getCustomerById(this.idCustomer).then(x => {
          if (x.successfully && x.data != null) {
            this.customerEdit = x.data;
            this.fillField(x.data);
          }
        });
      }
    });
  }

  get fNew() { return this.customerUpdateForm.controls; }
  get fdNewValid() { return this.customerUpdateForm.valid; }

  fillField(data) {
    debugger;
    this.customerUpdateForm.setValue({
      name: data.name,
      country: data.idCountry,
      city: data.city,
      address: data.address,
      phone: data.phone,
      email: data.email,
      taxId: data.taxId,
      contactPerson: data.contactPerson,
    });
  }

  onSubmit() {
    this.submittedNew = true;
    if (this.customerUpdateForm.invalid) {
      return;
    }

    debugger;
    this.customerService.updateProfile(this.customerUpdateForm.value).then(x => {
      if (x.successfully) {
      }
    });
  }

}

