import { Component, OnInit, Input } from '@angular/core';
import { Options } from 'select2';
import { FormBuilder, FormGroup, Validators, FormArray, FormControl } from '@angular/forms';
import { CategoryGoodService } from '../services/category-good.service';
import { AvailableCoverageService } from '../services/available-coverage.service';
import { ProviderService } from '../services/provider.service';
import { CustomerService } from '../services/customer.service';
import { NgbDateParserFormatter, NgbDateStruct, NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { CustomDateParserFormatter } from '../helper/custom-date-parser-formatter';
import { DecimalPipe } from '@angular/common';
import { UserService } from '../services/user.service';
import { BusinessQuoteService } from '../services/business-quote.service';
import { PremiumSetupService } from '../services/premium-setup.service';
import { ActivatedRoute } from "@angular/router";
import { Router } from "@angular/router";
import { ModalConfirmationComponent } from '../modal-confirmation/modal-confirmation.component'

@Component({
  selector: 'ngbd-modal-content',
  template: `
    <div class="modal-body">
      <app-insured [isConsignee]="isConsignee" [isCompany]="isCompany"></app-insured>
    </div>
    <div class="modal-footer">
      <button type="button" class="btn btn-outline-dark" (click)="activeModal.close('Close click')">Close</button>
    </div>
  `
})
export class NgbdModalCompanyContent {
  @Input() isConsignee;
  @Input() isCompany;

  constructor(public activeModal: NgbActiveModal) { }
}

@Component({
  selector: 'app-business-quote',
  templateUrl: './business-quote.component.html',
  styleUrls: ['./business-quote.component.css'],
  providers: [
    { provide: NgbDateParserFormatter, useClass: CustomDateParserFormatter },
    DecimalPipe
  ]
})


export class BusinessQuoteComponent implements OnInit {
  public optionsSelect: Options;
  public optionsSelectSimple: Options;
  public categoryGood: any = [];
  public availableCovers: any = [];
  public providers: any[];
  public customers: any[];
  public submitted: boolean = false;
  //public companyInfo = null;
  public currentUser: any;
  public needValidation: boolean = false;
  public businessQuoteEdit: any = null;
  public idBusinessQuoteRoute: number = 0;
  public authorized = true;
  public showAcceptReject = true;
  public reasonUnauthorized;
  public action: string = 'submit';
  public process: string = null;
  public userRole: string;
  public businessQuoteRate: string;
  public businessQuoteDetail: any[] = null;
  public enableEdit = false;
  public enableEditIndex = null;
  rateEdit = new FormControl('', Validators.required);

  businessQuoteForm: FormGroup;
  @Input() isModal = false;
  @Input() idBusinessQuote: number = null;
  @Input() idExecutive: number = null;
  @Input() readOnly = false;
  @Input() businessActiveModal;
  @Input() publicSite: boolean = false;

  constructor(
    private formBuilder: FormBuilder,
    public categoryService: CategoryGoodService,
    public availableService: AvailableCoverageService,
    public providerService: ProviderService,
    public customerService: CustomerService,
    private modalService: NgbModal,
    private userService: UserService,
    private businessQuoteService: BusinessQuoteService,
    private premiumSetupService: PremiumSetupService,
    private route: ActivatedRoute,
    private router: Router,
    private modalConfirmation: NgbModal,
  ) { }

  ngOnInit() {
    this.idBusinessQuoteRoute = +this.route.snapshot.paramMap.get("businessQuoteId");
    this.businessQuoteForm = this.formBuilder.group({
      dateOfIssue: [{ value: '', disabled: this.readOnly }, Validators.required],
      categoryGood: [{ value: '', disabled: this.readOnly }, Validators.required],
      available: [{ value: '', disabled: this.readOnly }, Validators.required],
      provider: [{ value: '', disabled: this.readOnly }, Validators.required],
      customer: [{ value: '', disabled: this.readOnly }, Validators.required]
    });

    this.currentUser = this.userService.getCurrentUser();
    if (this.currentUser == undefined) {
      this.router.navigate(['/login']);
      return;
    }
    this.verifyUserRole();
    let idUserExecutive;
    if (this.userRole.indexOf('Executive') != -1) {
      idUserExecutive = this.currentUser.localUser.idUser;
    } else {
      idUserExecutive = this.idExecutive;
    }
    if (idUserExecutive != null)
      this.loadSelect2Data(idUserExecutive);

    this.optionsSelect = {
      placeholder: "Select option...",
      allowClear: true,
      multiple: true,
      width: "100%"
    };

    this.optionsSelectSimple = {
      placeholder: "Select customer...",
      allowClear: false,
      multiple: false,
      width: "100%",
    };
    if (this.idBusinessQuote != null || (this.idBusinessQuoteRoute != null && this.idBusinessQuoteRoute > 0)) {
      if (this.userRole.indexOf('Supervisor') == -1 && this.idBusinessQuoteRoute != null && this.idBusinessQuoteRoute > 0) {
        this.reasonUnauthorized = 'Not authorized';
        this.authorized = false;
      } else {
        let id = this.idBusinessQuote != null ? this.idBusinessQuote : this.idBusinessQuoteRoute;
        this.businessQuoteService.GetBusinessQuoteById(id).then(x => {
          if (x.successfully) {
            let result: any = x.data;
            if (result.state != 2 && !this.readOnly) {
              this.reasonUnauthorized = 'This Business Quote has already been processed';
              this.authorized = false;
              return;
            }
            this.loadSelect2Data(result.idUser);
            this.businessQuoteEdit = result;
            let fecha = new Date(result.dateOfIssue);
            //this.companyInfo = result.insured;
            this.businessQuoteForm.get("dateOfIssue").setValue({ year: fecha.getFullYear(), month: fecha.getMonth() + 1, day: fecha.getDate() });
            this.businessQuoteForm.get("categoryGood").setValue(result.categoryGood.split(','));
            this.businessQuoteForm.get("available").setValue(result.availableCoverage.split(','));
            this.businessQuoteForm.get("provider").setValue(result.provider.split(','));
            if (!!result.idCustomer) {
              this.businessQuoteForm.get("customer").setValue(result.idCustomer);
            }

            this.businessQuoteDetail = result.businessQuoteDetails.map(x => {
              return {
                "idProvider": x.idProvider,
                "idCategoryGood": x.idCategoryGood,
                "idAvailableCoverage": x.idAvailableCoverage,
                "providerName": x.provider.nameProvider,
                "availableCoverageDescription": x.AvailableCoverage.description,
                "categoryGoodDescription": x.CategoryGood.description,
                "rateValue": x.rateValue
              }
            });
          }
          else {
            this.reasonUnauthorized = x.reason;
            this.authorized = false;
          }
        });
      }
    }
  }

  loadSelect2Data(idUserExecutive) {
    this.premiumSetupService.GetPremiumCommissionList(idUserExecutive).then(x => {
      if (x.successfully) {
        if (x.data.length == 0) {
          this.reasonUnauthorized = `The executive doesn't have a rate configuration`;
          this.authorized = false;
          return;
        } else {
          this.businessQuoteRate = JSON.stringify(x.data);
          let rate = x.data;
          let categoryArray = rate.map(x => {
            return x.idCategoryGood;
          });
          let availableArray = rate.map(x => {
            return x.idAvailableCoverage;
          });
          let providerArray = rate.map(x => {
            return x.idProvider;
          });

          this.categoryService.getCategoryGood()
            .then(x => {
              this.categoryGood = [];
              x.data.forEach(category => {
                if (categoryArray.find(x => x == category.idCategoryGood) !== undefined) {
                  this.categoryGood.push({ id: category.idCategoryGood, text: category.description });
                }
              });
            });

          this.availableService.getAvailableCoverage()
            .then(x => {
              this.availableCovers = [];
              x.data.forEach(available => {
                if (availableArray.find(x => x == available.idAvailableCoverage) !== undefined) {
                  this.availableCovers.push({ id: available.idAvailableCoverage, text: available.description });
                }
              });
            });

          this.providerService.getProviderList()
            .then(x => {
              this.providers = [];
              x.data.forEach(provider => {
                if (providerArray.find(x => x == provider.idProvider) !== undefined) {
                  this.providers.push({ id: provider.idProvider, text: provider.nameProvider });
                }
              });
            });

          this.customerService.getAllCustomerList('')
            .then(x => {
              this.customers = [];
              if (x.successfully) {
                x.data.forEach(customer => {
                  this.customers.push({ id: customer.idCustomer, text: customer.name + ' (' + customer.email + ' - ' + customer.taxId + ')'  });
                });
              }
            });
        }
      }
    });
  }

  onSubmit() {
    debugger;
    this.submitted = true;

    if (this.businessQuoteForm.invalid) {
      return;
    }
    if (this.isModal == false) {
      if (this.action == 'submit' || this.action == 'saveAndSend') {
        this.businessQuoteService.createBusinessQuote(this.businessQuoteForm.value, this.needValidation, this.currentUser.localUser.idUser, this.businessQuoteDetail).then(x => {
          if (x.data != null) {
            let result: any = x.data;
            if (this.needValidation) {
              this.process = `Business Quote Created with ID = ${result.idBusinessQuote}, waiting for supervisor approval`;
            } else {
              this.process = `Business Quote Created with ID = ${result.idBusinessQuote} and sent by email`;
            }
            this.onReset();
          }
        });
      }
      else if (this.action == 'saveAndDownload') {

        this.businessQuoteService.createBusinessQuoteAndDownload(this.businessQuoteForm.value, this.needValidation, this.currentUser.localUser.idUser, this.businessQuoteDetail).then(x => {
          if (x.data != null) {
            let result: any = x.data;
            if (this.needValidation) {
              this.process = `Business Quote Created with ID = ${result.idBusinessQuote}, waiting for supervisor approval`;
            } else {
              this.process = `Business Quote Created with ID = ${result.idBusinessQuote} and download pdf`;

              //var sampleArr = this.base64ToArrayBuffer(Object.values(x.dataOptional)[0]);
              //this.saveByteArray(Object.keys(x.dataOptional)[0], sampleArr);

              //var sampleArr2 = this.base64ToArrayBuffer(Object.values(x.dataOptional)[1]);
              //this.saveByteArray(Object.keys(x.dataOptional)[1], sampleArr2);
              debugger;
              var files = Object.keys(x.dataOptional);
              for (var i = 0; i < files.length; i++) {
                var sampleArr = this.base64ToArrayBuffer(Object.values(x.dataOptional)[i]);
                this.saveByteArray(Object.keys(x.dataOptional)[i], sampleArr);
              }


            }
            this.onReset();
          }
        });
      }
      else if (this.action == 'process') {
        this.processDetail();
      }
      else {
        this.businessQuoteEdit.state = this.action == 'approve' ? 3 : 4;
        this.businessQuoteService.updateBusinessQuote(this.businessQuoteForm.value, this.businessQuoteEdit, this.businessQuoteDetail).then(x => {
          if (x.successfully) {
            this.process = this.action == 'approve' ? 'Business Quote Approve' : 'Business Quote Reject';
            this.onReset();
          }
        });
      }
    }
    else {
      if (this.action == 'process') {
        this.processDetail();
      } else {
        this.businessQuoteService.updateBusinessQuote(this.businessQuoteForm.value, this.businessQuoteEdit, this.businessQuoteDetail).then(x => {
          if (x.successfully) {
            this.businessActiveModal.close('Ok');
          }
        });
      }
    }
  }

  close() {
    this.process = null;
  }

  onApprove() {
    this.action = 'approve';
  }

  onReject() {
    this.action = 'reject';
  }

  onProcess() {
    debugger;
    this.action = 'process';
  }

  onSave() {
    this.action = 'submit';
  }

  onSaveAndSend() {
    this.action = 'saveAndSend';
  }

  onSaveAndDownload() {
    this.action = 'saveAndDownload';
  }

  processDetail() {
    this.businessQuoteDetail = [];
    this.needValidation = false;
    let newArray = JSON.parse(this.businessQuoteRate);
    newArray.forEach(x => {
      if (this.businessQuoteForm.value.provider.find(y => x.idProvider == y) && this.businessQuoteForm.value.categoryGood.find(y => x.idCategoryGood == y)
        && this.businessQuoteForm.value.available.find(y => x.idAvailableCoverage == y))
        this.businessQuoteDetail.push(x);
    });
    if (this.businessQuoteDetail.length == 0)
      this.businessQuoteDetail = null;
  }

  get f() { return this.businessQuoteForm.controls; }

  get fValid() { return this.businessQuoteForm.valid; }

  onReset() {
    debugger;
    this.submitted = false;
    //this.companyInfo = null;
    this.needValidation = false;
    this.action = 'submit';
    this.businessQuoteDetail = null;
    this.businessQuoteForm.reset();
    this.businessQuoteForm.controls["provider"].patchValue('');
  }

  validateRate() {
    if (this.isModal || this.businessQuoteEdit != null)
      return;
    this.needValidation = false;
    let rateBusinessQuote = JSON.parse(this.businessQuoteRate);
    this.businessQuoteDetail.forEach(x => {
      let currentItem = rateBusinessQuote.find(y => y.idProvider == x.idProvider && y.idCategoryGood == x.idCategoryGood && y.idAvailableCoverage == x.idAvailableCoverage);
      if (currentItem != null) {
        if (x.rateValue < currentItem.rateValue) {
          this.needValidation = true;
          return;
        }
      }
    });
  }

  //callInsured() {
  //  const modalRef = this.modalService.open(NgbdModalCompanyContent);
  //  modalRef.componentInstance.isConsignee = false;
  //  modalRef.componentInstance.isCompany = true;
  //  modalRef.result.then((result) => {
  //    if (typeof result != 'string')
  //      this.companyInfo = result;
  //  }).catch(x => { });
  //}

  verifyUserRole() {
    if (this.currentUser != null) {
      if (this.currentUser.localUser != null && this.currentUser.localUser.userRoles != null) {
        this.userRole = "";
        this.userRole += this.currentUser.localUser.userRoles.map(x => {
          return `${x.role.roleDescription}-`;
        });
      } else {
        this.userRole = "";
      }
    } else {

    }
  }

  enableEditMethod(e, i, rateValue) {
    this.enableEdit = true;
    this.enableEditIndex = i;
    this.rateEdit.setValue(rateValue);
  }

  SaveChange(idProvider, idCategoryGood, idAvailableCoverage) {
    debugger;
    let itemUpdate = this.businessQuoteDetail.find(x => x.idProvider == idProvider && x.idCategoryGood == idCategoryGood && x.idAvailableCoverage == idAvailableCoverage);
    if (itemUpdate != null)
      itemUpdate.rateValue = this.rateEdit.value;

    this.validateRate();
    this.enableEdit = false;
    this.enableEditIndex = null;
  }

  Delete(idProvider, idCategoryGood, idAvailableCoverage) {
    const modalRef = this.modalConfirmation.open(ModalConfirmationComponent);
    modalRef.componentInstance.title = "Delete Record";
    modalRef.componentInstance.message = "Are you sure you want to delete the selected item?";
    modalRef.result.then((result) => {
      if (result) {
        let indexFind = this.businessQuoteDetail.findIndex(x => x.idProvider == idProvider && x.idCategoryGood == idCategoryGood && x.idAvailableCoverage == idAvailableCoverage);
        if (indexFind != null) {
          this.businessQuoteDetail.splice(indexFind, 1);
        }
      }
    }).catch(x => { });
  }

  base64ToArrayBuffer(base64) {
    var binaryString = window.atob(base64);
    var binaryLen = binaryString.length;
    var bytes = new Uint8Array(binaryLen);
    for (var i = 0; i < binaryLen; i++) {
      var ascii = binaryString.charCodeAt(i);
      bytes[i] = ascii;
    }
    return bytes;
  }

  saveByteArray(reportName, byte) {
    var blob = new Blob([byte], { type: "application/pdf" });
    var link = document.createElement('a');
    link.href = window.URL.createObjectURL(blob);
    var fileName = reportName;
    link.download = fileName;
    link.click();
  };
}
