import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';
import { QuestionService } from './lib/question.service';
import { QuestionGroupBase } from './lib/question-group-base';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-test-form-dynamic',
  template: `
    <div class="container main-container margin-container">
      <app-dynamic-form [questionsGroup]="questionsGroups$  | async" *ngIf="loaded"></app-dynamic-form>
    </div>
  `,
  providers: [QuestionService]
})
export class TestFormDynamic implements OnInit {
  questionsGroups$: Observable<QuestionGroupBase[]>;
  loaded: boolean;
  form: FormGroup;

  constructor(private service: QuestionService, private formBuilder: FormBuilder) {
    this.loaded = false;
  }

  ngOnInit() {
    this.service.getQuestionsGroup(2).then(x => {
      this.questionsGroups$ = x;
      this.loaded = true;
    });
  }

}
