import { Component, OnInit } from '@angular/core';
import { Options } from 'select2';
import { AuthService } from "angularx-social-login";
import { GoogleLoginProvider } from "angularx-social-login";
import { FormBuilder, FormGroup, Validators, FormControl, FormArray } from '@angular/forms';
import { NgbModal, ModalDismissReasons, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { UserService } from '../../services/user.service';
import { ClientService } from '../../services/client.service';
import { RoleService } from '../../services/role.service';
import { ModalConfirmationComponent } from '../../modal-confirmation/modal-confirmation.component'
import { Router } from '@angular/router';

@Component({
  selector: 'app-adm-users',
  templateUrl: './adm-users.component.html',
  styleUrls: ['./adm-users.component.css']
})
export class AdmUsersComponent implements OnInit {
  public submitted: boolean;
  public submittedNew: boolean;
  usersListForm: FormGroup;
  userCreateForm: FormGroup;
  public showMessage: boolean = false;
  public usersList: any = [];
  public usersListFilter: any = [];
  public enableEdit = false;
  public enableEditIndex = null;
  public userEdit: any = null;
  public userFilter;
  public role: any = [];
  public clientList: any[];
  modalNew: NgbModalRef;
  filter = new FormControl('');
  closeResult = '';
  public optionsSelect: Options;
  public submitOptionName = 'Create';

  constructor(
    private authService: AuthService,
    private formBuilder: FormBuilder,
    private userService: UserService,
    private clientService: ClientService,
    private roleService: RoleService,
    private router: Router,
    private modalService: NgbModal,
    private modalConfirmation: NgbModal
  ) { }

  ngOnInit() {
    this.usersListForm = this.formBuilder.group({
      name: ['', Validators.required],
      email: ['', Validators.required],
      password: [''],
      client: ['']
    });
    this.userCreateForm = this.formBuilder.group({
      name: ['', Validators.required],
      email: ['', Validators.required],
      password: [''],
      client: ['', Validators.required],
      role: ['', Validators.required],
      guidUser: ['']
    });

    this.optionsSelect = {
      placeholder: "Select option...",
      allowClear: true,
      multiple: true,
      width: "100%"
    };

    this.loadData();
    this.filter.valueChanges.subscribe(value => {
      this.usersListFilter = this.search(value);
    });

  }

  get f() { return this.usersListForm.controls; }
  get fNew() { return this.userCreateForm.controls; }
  get fdValid() { return this.usersListForm.valid; }
  get fdNewValid() { return this.userCreateForm.valid; }

  loadData() {
    this.userService.getUsersByClient(null)
      .then(x => {
        this.usersList = x.data;
        this.usersListFilter = this.search(this.filter.value);
      });

    this.roleService.getRoleList()
      .then(x => {
        this.role = [];
        x.data.forEach(role => {
          this.role.push({ id: role.idRole, text: role.roleDescription });
        });
      });

    this.clientService.getClientList()
      .then(x => this.clientList = x.data);
  }

  search(text: string): any[] {
    return this.usersList.filter(user => {
      const term = text.toLowerCase();
      return !!user.name && user.name.toString().toLowerCase().includes(term)
        || !!user.email && user.email.toString().toLowerCase().includes(term)
        || !!user.guidUser && user.guidUser.toString().toLowerCase().includes(term);
    });
  }

  onSubmit() {
    this.submitted = true;
    this.showMessage = false;
    if (this.usersListForm.invalid) {
      return;
    }
  }

  enableEditMethod(e, i, categoryGoodRow) {
    this.enableEdit = true;
    this.enableEditIndex = i;
    this.usersListForm.setValue({
      description: categoryGoodRow.description,
      coverageDescription: categoryGoodRow.coverageDescription
    });
  }

  Cancel() {
    this.enableEdit = false;
    this.enableEditIndex = null;
  }

  Delete(idUser) {
    //const modalRef = this.modalConfirmation.open(ModalConfirmationComponent);
    //modalRef.componentInstance.title = "Delete Record";
    //modalRef.componentInstance.message = "Are you sure you want to delete the selected item?";
    //modalRef.result.then((result) => {
    //  if (result) {
    //    this.userService.deleteUserSupervisor(idCategoryGood)
    //      .then(x => {
    //        if (x.successfully)
    //          this.loadData();
    //      });
    //  }
    //}).catch(x => { });

  }

  SaveChange(idUser) {
    this.userService.updateUserItem(this.usersListForm.value)
      .then(x => {
        if (x.successfully) {
          this.Cancel();
          this.loadData();
        }
      });
  }

  New(content) {
    this.submitOptionName = 'Create';
    this.ClearNew();
    this.modalNew = this.modalService.open(content, { ariaLabelledBy: 'modal-basic-title' });
    this.modalNew.result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }

  Edit(id, content) {
    this.submitOptionName = 'Update';
    this.userService.getUserById(id).then(x => {
      if (x.successfully && x.data != null) {
        this.userEdit = x.data;
        this.fillField(x.data);

        this.modalNew = this.modalService.open(content, { ariaLabelledBy: 'modal-basic-title' });
        this.modalNew.result.then((result) => {
          this.closeResult = `Closed with: ${result}`;
        }, (reason) => {
          this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
        });
      }
    });
  }

  fillField(data) {
    debugger;
    let roles = data.userRoles.map(x => { return x.idRole.toString() });
    this.userCreateForm.setValue({
      email: data.email,
      name: data.name,
      password: data.password,
      client: data.idClient,
      role: roles,
      guidUser: data.guidUser
    });
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }

  ClearNew() {
    this.userCreateForm.setValue({
      name: '',
      email: '',
      password: '',
      client: '',
      role: [],
      guidUser: ''
    });
  }

  onSubmitNew() {
    this.submittedNew = true;

    if (this.userCreateForm.invalid) {
      return;
    }

    this.CreateNew();
  }
  CreateNew() {

    if (this.userEdit != null) {
      this.userService.updateUser(this.userCreateForm.value, this.userEdit).then(x => {
        if (x.successfully) {
          this.onReset();
        }
      });
    } else {
      this.userService.createUser(this.userCreateForm.value).then(x => {
        if (x.successfully) {
          this.onReset();
        }
      });
    }
  }

  onReset() {
    this.modalNew.close();
    this.ClearNew();
    this.loadData();
    this.userCreateForm.reset();
  }

  isSupervisor() {
    if (!!this.userEdit) {
      let roles = this.userEdit.userRoles.map(x => x.idRole);
      return (roles.includes(4));
    } else {
      return false;
    }
  }
}

