import { Injectable, Inject } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { UserService } from '../../app/services/user.service';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class PremiumSetupService {
  private baseApiUrl: string;

  constructor(
    private http: HttpClient, @Inject('BASE_API_URL') baseApiUrl: string,
    private userService: UserService,
    private router: Router
  ) {
    this.baseApiUrl = baseApiUrl;
  }

  //GET
  async GetPremiumCommission(IdPremiumCommission: number): Promise<ResponseApi> {
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}PremiumCommission/GetPremiumCommission?IdPremiumCommission=${IdPremiumCommission}`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //GET
  async GetPremiumCommissionList(idExecutive: number): Promise<ResponseApi> {
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}PremiumCommission/GetPremiumCommissionList?idExecutive=${idExecutive}`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  async GetPremiumCommissionListOfCustomer(idCustomer: number): Promise<ResponseApi> {
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}PremiumCommission/GetPremiumCommissionListOfCustomer?idCustomer=${idCustomer}`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  

  //POST
  createPremiumCommission(premiumForm: any, idExecutive: number): Promise<ResponseApi> {
    let premium = { "idProvider": +premiumForm.provider, "idAvailableCoverage": +premiumForm.available, "idCategoryGood": +premiumForm.categoryGood, "idClient": +premiumForm.client, "idCommission": +premiumForm.commission, "idExecutive": +idExecutive, "rateValue": this.removeFormatNumber(premiumForm.rateValue) };
    try {
      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}PremiumCommission/CreatePremiumCommission`, JSON.stringify(premium), this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  //PUT
  updatePremiumCommission(premiumOld: string, pUp: any, idExecutive: number): Promise<ResponseApi> {
    let premiumUpd = `${pUp.provider}|${pUp.available}|${pUp.categoryGood}|${pUp.client}|${pUp.commission}|${pUp.rateValue}|${idExecutive}|${pUp.idPremiumCommission}`;
    try {
      let response = this.http
        .put<ResponseApi>(`${this.baseApiUrl}PremiumCommission/UpdatePremiumCommission?premiumOld=${premiumOld}&premiumUpd=${premiumUpd}`, null, this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  createPremiumCommission2(premiumForm: any): Promise<ResponseApi> {
    let premium = { "idPremiumCommission": +premiumForm.idPremiumCommission, "idProvider": +premiumForm.provider, "idAvailableCoverage": +premiumForm.available, "idCategoryGood": +premiumForm.categoryGood, "idClient": +premiumForm.client, "idCommission": +premiumForm.commission, "idCustomer": +premiumForm.idCustomer, "rateValue": this.removeFormatNumber(premiumForm.rateValue) };
    try {
      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}PremiumCommission/CreatePremiumCommission`, JSON.stringify(premium), this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  updatePremiumCommission2(premiumForm: any): Promise<ResponseApi> {
    let premium = { "idPremiumCommission": +premiumForm.idPremiumCommission, "idProvider": +premiumForm.provider, "idAvailableCoverage": +premiumForm.available, "idCategoryGood": +premiumForm.categoryGood, "idClient": +premiumForm.client, "idCommission": +premiumForm.commission, "idCustomer": +premiumForm.idCustomer, "rateValue": this.removeFormatNumber(premiumForm.rateValue) };
    try {
      let response = this.http
        .put<ResponseApi>(`${this.baseApiUrl}PremiumCommission/UpdatePremiumCommission2`, JSON.stringify(premium), this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  //GET
  async DeletePremiumCommission(idPremiumCommission: number): Promise<ResponseApi> {
    try {
      let response = await this.http
        .delete(`${this.baseApiUrl}PremiumCommission/DeletePremiumCommission?idPremiumCommission=${idPremiumCommission}`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //DELETE
  async DeletePremiumCommissionList(listPremium): Promise<ResponseApi> {
    const httpOptionsDelete = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${this.userService.getToken()}`
      }),
      body: JSON.stringify(listPremium)
    };
    try {
      let response = await this.http
        .delete(`${this.baseApiUrl}PremiumCommission/DeletePremiumCommissionList`, httpOptionsDelete)
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  removeFormatNumber(value) {
    return parseFloat(value.toString().replace(',', ''));
  }

  // Error handling
  errorHandl(error) {
    let errorMessage = '';
    if (error.error instanceof ErrorEvent) {
      // Get client-side error
      errorMessage = error.error.message;
    } else {
      // Get server-side error
      errorMessage = `Error Code: ${error.status}\nMessage: ${error.message}`;
    }
    console.log(errorMessage);

    if (error.status == 401) {
      this.userService.signOut();
      this.router.navigate(['login']);
    }

    return throwError(errorMessage);
  }

}

interface ResponseApi {
  successfully: boolean
  reason: string
  data: any
}
