import { Component, OnInit } from '@angular/core';
import { Options } from 'select2';
import { FormBuilder, FormGroup, Validators, FormArray } from '@angular/forms';
import { UserService } from '../services/user.service';
import { ClientService } from '../services/client.service';
import { RoleService } from '../services/role.service';
import { ActivatedRoute } from "@angular/router";

@Component({
  selector: 'app-user-manager',
  templateUrl: './user-manager.component.html',
  styleUrls: ['./user-manager.component.css']
})
export class UserManagerComponent implements OnInit {
  public userIdAct = null;
  userForm: FormGroup;
  public optionsSelect: Options;
  public role: any = [];
  public clientList: any[];
  public submitted: boolean = false;
  public userEdit: any = null;

  constructor(
    private route: ActivatedRoute,
    private formBuilder: FormBuilder,
    public userService: UserService,
    public clientService: ClientService,
    public roleService: RoleService
  ) { }

  ngOnInit() {

    if (!!this.route.snapshot.paramMap.get("id")) {
      this.userIdAct = this.route.snapshot.paramMap.get("id");
      this.userService.getUserById(this.userIdAct).then(x => {
        if (x.successfully && x.data != null) {
          this.userEdit = x.data;
          this.fillField(x.data);
        }
      });
    }

    this.userForm = this.formBuilder.group({
      email: ['', Validators.required],
      name: ['', Validators.required],
      password: ['', Validators.required],
      client: ['', Validators.required],
      role: ['', Validators.required],
      guidUser: ['']
    });


    this.roleService.getRoleList()
      .then(x => {
        this.role = [];
        x.data.forEach(role => {
          this.role.push({ id: role.idRole, text: role.roleDescription });
        });
      });

    this.clientService.getClientList()
      .then(x => this.clientList = x.data);

    this.optionsSelect = {
      placeholder: "Select option...",
      allowClear: true,
      multiple: true,
      width: "100%"
    };
  }

  onSubmit() {
    this.submitted = true;

    if (this.userForm.invalid) {
      return;
    }

    if (this.userEdit != null) {
      this.userService.updateUser(this.userForm.value, this.userEdit).then(x => {
        if (x.successfully) {
          this.onReset();
        }
      });
    } else {
      this.userService.createUser(this.userForm.value).then(x => {
        if (x.successfully) {
          this.onReset();
        }
      });
    }

  }

  get f() { return this.userForm.controls; }

  findByFilter(type) {
    let filter = '';
    if (type == 'email') {
      filter = this.userForm.controls["email"].value;
    } else {
      filter = this.userForm.controls["name"].value;
    }
    if (filter != '') {
      this.userService.getUserByFilter(filter).then(x => {
        if (x.successfully && x.data != null) {
          this.userEdit = x.data;
          this.fillField(x.data);
        }
      });
    }
  }

  fillField(data) {
    let roles = data.userRoles.map(x => { return x.idRole.toString() });
    this.userForm.setValue({
      email: data.email,
      name: data.name,
      password: data.password,
      client: data.idClient,
      role: roles,
      guidUser: data.guidUser
    });
  }

  onReset() {
    this.submitted = false;
    this.userEdit = null;
    this.userForm.reset();
  }


}
