import { Injectable, Inject } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { UserService } from '../../app/services/user.service';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class TypeShipmentService {
  private baseApiUrl: string;

  constructor(
    private http: HttpClient, @Inject('BASE_API_URL') baseApiUrl: string,
    private userService: UserService,
    private router: Router
  ) {
    this.baseApiUrl = baseApiUrl;
  }

  //GET
  async getTypeShipmentList(): Promise<ResponseApi> {
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}TypeShipment/GetTypeShipmentList`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  // Error handling
  errorHandl(error) {
    let errorMessage = '';
    if (error.error instanceof ErrorEvent) {
      // Get client-side error
      errorMessage = error.error.message;
    } else {
      // Get server-side error
      errorMessage = `Error Code: ${error.status}\nMessage: ${error.message}`;
    }
    console.log(errorMessage);

    if (error.status == 401) {
      this.userService.signOut();
      this.router.navigate(['login']);
    }

    return throwError(errorMessage);
  }
}

interface ResponseApi {
  successfully: boolean
  reason: string
  data: any[]
}
