import { Injectable, Inject } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { UserService } from '../../app/services/user.service';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class InsuredService {
  private baseApiUrl: string;

  constructor(
    private http: HttpClient, @Inject('BASE_API_URL') baseApiUrl: string,
    private userService: UserService,
    private router: Router
  ) {
    this.baseApiUrl = baseApiUrl;
  }

  //GET
  async getAllInsuredList(filter): Promise<ResponseApi> {
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}Insured/GetAllList?filter=${filter}`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //GET
  async getInsuredList(isConsignee): Promise<ResponseApi> {
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}Insured/GetInsuredList?isConsignee=${isConsignee}`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //GET
  async getInsuredById(id): Promise<ResponseApi> {
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}Insured/GetInsuredById?idInsured=${id}`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //GET
  async getInsuredByEmail(email): Promise<ResponseApi> {
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}Insured/GetInsuredByEmail?email=${email}`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //GET
  async getCompanyByEmail(email): Promise<ResponseApi> {
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}Insured/GetCompanyByEmail?email=${email}`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //GET
  async getCompanyList(): Promise<ResponseApi> {
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}Insured/GetCompanyList`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //GET
  async getAllInsuredWithoutCustomer(): Promise<ResponseApi> {
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}CustomerInsured/GetAllInsuredWithoutCustomer`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //POST
  createInsured(insuredForm: any, isConsignee: boolean, isCompany: boolean, isAdmin: number=0): Promise<ResponseApi> {
    let insured = this.buildJson(insuredForm, isConsignee, isCompany, null);
    try {
      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}Insured/CreateInsured?isAdmin=${isAdmin}`, JSON.stringify(insured), this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  //POST
  updateInsured(insuredForm: any, isConsignee: boolean, isCompany: boolean, idInsured: number): Promise<ResponseApi> {
    let insured = this.buildJson(insuredForm, isConsignee, isCompany, idInsured);
    try {
      let response = this.http
        .put<ResponseApi>(`${this.baseApiUrl}Insured/UpdateInsured`, JSON.stringify(insured), this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  buildJson(insuredForm: any, isConsignee: boolean, isCompany: boolean, idInsured:number) {
    return {
      "idInsured": !!idInsured ? idInsured: 0,
      "name": insuredForm.name,
      "idCountry": +insuredForm.country,
      "city": insuredForm.city,
      "address": insuredForm.address,
      "phone": insuredForm.phone.toString(),
      "email": insuredForm.email,
      "isConsignee": isConsignee,
      "isCompany": isCompany,
      "contactPerson": insuredForm.contactPerson,
      "taxId": insuredForm.taxId,
      "creditLimit": insuredForm.creditLimit
    }
  }

  // InsuredExecutive //
  //POST
  createInsuredExecutive(valueForm): Promise<ResponseApi> {
    let insuredExecutive = { "idInsured": +valueForm.insured, "idExecutive": +valueForm.executive };
    try {
      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}InsuredExecutive/CreateInsuredExecutive`, JSON.stringify(insuredExecutive), this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  //GET
  async getInsuredExecutiveList(idInsured: number = null): Promise<ResponseApi> {
    let idInsuredStr = '';
    if (!!idInsured) idInsuredStr = `/${idInsured}`;
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}InsuredExecutive/GetInsuredExecutiveList` + idInsuredStr, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //DELETE
  async deleteInsuredExecutive(idInsured: number, idExecutive: number): Promise<ResponseApi> {
    try {
      let response = await this.http
        .delete(`${this.baseApiUrl}InsuredExecutive/DeleteInsuredExecutive?idInsured=${idInsured}&idExecutive=${idExecutive}`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  // Error handling
  errorHandl(error) {
    let errorMessage = '';
    if (error.error instanceof ErrorEvent) {
      // Get client-side error
      errorMessage = error.error.message;
    } else {
      // Get server-side error
      errorMessage = `Error Code: ${error.status}\nMessage: ${error.message}`;
    }
    console.log(errorMessage);

    if (error.status == 401) {
      this.userService.signOut();
      this.router.navigate(['login']);
    }

    return throwError(errorMessage);
  }
}

interface ResponseApi {
  successfully: boolean
  reason: string
  data: any[]
}
