import { Component, OnInit, Input } from '@angular/core';
import { Validators, FormGroup, FormBuilder } from '@angular/forms';
import { NgbModal, ModalDismissReasons, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { ModalConfirmationComponent } from '../modal-confirmation/modal-confirmation.component'
import { CustomerService } from '../services/customer.service';
import { InsuredService } from '../services/insured.service';
import { CountryService } from '../services/country.service';

@Component({
  selector: 'app-customer-insured',
  templateUrl: './customer-insured.component.html',
  styleUrls: ['./customer-insured.component.css']
})
export class CustomerInsuredComponent implements OnInit {
  @Input() idCustomerFilter: number = null;
  @Input() allowAssingUser: boolean = true;
  @Input() allowEditUser: boolean = true;
  public countries: any[];
  submitted = false;
  public submittedNew: boolean;
  insuredForm: FormGroup;
  modalEdit: NgbModalRef;
  public customerInsuredList: any;
  public message: string;
  public companys: any[];
  public insuredsFree: any[];
  customerInsuredForm: FormGroup;
  public insuredEdit;

  constructor(
    private modalConfirmation: NgbModal,
    public customerService: CustomerService,
    public insuredService: InsuredService,
    public countryService: CountryService,
    private formBuilder: FormBuilder,
    private modalService: NgbModal,
  ) { }

  ngOnInit() {
    this.customerInsuredForm = this.formBuilder.group({
      customer: !!this.idCustomerFilter ? [''] : ['', Validators.required],
      insured: ['', Validators.required]
    });

    this.loadDataInsuredsFree();
    this.loadData();

    this.insuredForm = this.formBuilder.group({
      name: ['', Validators.required],
      country: ['', Validators.required],
      city: ['', Validators.required],
      address: ['', Validators.required],
      phone: ['', Validators.required],
      email: ['', Validators.required],
      taxId: ['', Validators.required],
      zipCode: [''],
    });
  }

  get fNew() { return this.insuredForm.controls; }
  get fdNewValid() { return this.insuredForm.valid; }

  loadData() {
    let idCustomer: number = null;
    if (!!this.idCustomerFilter) idCustomer = this.idCustomerFilter;
    this.customerService.getCustomerInsuredList(idCustomer).then(x => {
      this.customerInsuredList = x.data;
    });
    this.countryService.getCountries()
      .then(x => {
        this.countries = x.data
      });
  }

  loadDataInsuredsFree() {
    this.insuredService.getAllInsuredWithoutCustomer().then(x => {
      this.insuredsFree = x.data;
    });
  }

  onSubmit() {
    this.submitted = true;
    if (!!this.idCustomerFilter) this.customerInsuredForm.value.customer = this.idCustomerFilter;

    if (this.customerInsuredForm.invalid)
      return;
    this.submitted = false;
    this.customerService.createCustomerInsured(this.customerInsuredForm.value).then(x => {
      if (x.successfully) {
        this.loadData();
        this.loadDataInsuredsFree();
      } else {
        this.message = x.reason;
      }
    });
  }

  close() {
    this.message = null;
  }

  get f() { return this.customerInsuredForm.controls; }

  edit(contentModalEdit, customerExe) {
    this.insuredService.getInsuredById(customerExe.idInsured).then(x => {
      if (x.successfully && x.data != null) {
        this.submittedNew = false;
        this.insuredEdit = x.data;
        this.fillField(this.insuredEdit);
        this.modalEdit = this.modalService.open(contentModalEdit, { ariaLabelledBy: 'modal-basic-title', size: 'xl' });
        this.modalEdit.result.then((result) => {
        }, (reason) => {
        });
      }
    });
  }

  fillField(data) {
    debugger;
    this.insuredForm.setValue({
      name: data.name,
      country: data.idCountry,
      city: data.city,
      address: data.address,
      phone: data.phone,
      email: data.email,
      taxId: data.taxId,
      zipCode: data.zipCode
    });
  }

  onSubmitEdit() {
    this.submittedNew = true;

    if (this.insuredForm.invalid) {
      return;
    }
    this.insuredService.updateInsured(this.getDataEdit(), this.insuredEdit.isConsigne, this.insuredEdit.isCompany, this.insuredEdit.idInsured).then(x => {
      if (x.successfully && x.data != null) {
        this.loadData();
        this.modalEdit.close();
      }
    });
  }

  getDataEdit() {
    return {
      name: this.insuredForm.value.name,
      country: this.insuredForm.value.country,
      city: this.insuredForm.value.city,
      address: this.insuredForm.value.address,
      phone: this.insuredForm.value.phone,
      email: this.insuredForm.value.email,
      taxId: this.insuredForm.value.taxId,
      zipCode: this.insuredForm.value.zipCode,
      //isConsignee: this.insuredEdit.isConsignee,
      //isCompany: this.insuredEdit.isCompany,
      contactPerson: this.insuredEdit.contactPerson,
      creditLimit: this.insuredEdit.creditLimit,
    };
  }

  closeModal() {
    this.modalEdit.close();
  }

  Delete(idCustomer, idInsured) {
    const modalRef = this.modalConfirmation.open(ModalConfirmationComponent);
    modalRef.componentInstance.title = "Unassing Insured";
    modalRef.componentInstance.message = "Are you sure you want to unassign insured to this customer?";
    modalRef.result.then((result) => {
      if (result) {
        this.customerService.deleteCustomerInsured(idCustomer, idInsured)
          .then(x => {
            if (x.successfully) {
              this.loadData();
              this.loadDataInsuredsFree();
            } else {
              this.message = x.reason;
            }
          });
      }
    }).catch(x => { });
  }

}
