import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute } from "@angular/router";
import { FormBuilder, FormGroup } from '@angular/forms';
import { BusinessQuoteService } from '../services/business-quote.service';

@Component({
  selector: 'app-business-quote-accept.component',
  templateUrl: './business-quote-accept.component.html',
  styleUrls: ['./business-quote-accept.component.css']
})
export class BusinessQuoteAcceptComponent implements OnInit {
  public loading: boolean = false;
  public isModal: boolean = false;
  public idBusinessQuote: number;
  public idExecutive: number;
  public readOnly: boolean = true;
  public show: boolean = false;
  public guidValidate: string;
  public bq: any;
  public dateOfIssue: string;
  aceptedForm: FormGroup;

  constructor(
    public bQuoteService: BusinessQuoteService,
    private route: ActivatedRoute,
    private formBuilder: FormBuilder,
  ) {
  }

  ngOnInit() {

    this.aceptedForm = this.formBuilder.group({
      clientComments: ''
    });

    if (!!this.route.snapshot.paramMap.get("guidValidate")) {
      this.bQuoteService.GetBusinessQuoteByGuidValidate(this.route.snapshot.paramMap.get("guidValidate"))
        .then(x => {
          if (x.successfully) {
            this.idBusinessQuote = x.data.idBusinessQuote;
            this.idExecutive = x.data.idUser;
            this.guidValidate = this.route.snapshot.paramMap.get("guidValidate");
            this.aceptedForm.setValue({
              clientComments: x.data.clientComments,
            });
            this.bq = x.data;
            let fecha = new Date(this.bq.dateOfIssue)
            this.dateOfIssue = fecha.toDateString();
            this.show = true;
          }
        });
    } else {
      //redirijir al login
    }
  }

  acceptBQ() {
    this.loading = true;
    debugger;
    this.bQuoteService.AcceptBusinessQuoteByGuidValidate(this.route.snapshot.paramMap.get("guidValidate"), this.aceptedForm.value.clientComments)
      .then(x => {
        this.loading = false;
        if (x.successfully) {
          this.bq = x.data;
        }
      });
  }

  rejectBQ() {
    this.loading = true;
    debugger;
    this.bQuoteService.RejectionBusinessQuoteByGuidValidate(this.route.snapshot.paramMap.get("guidValidate"), this.aceptedForm.value.clientComments)
      .then(x => {
        this.loading = false;
        if (x.successfully) {
          this.bq = x.data;
        }
      });
  }

}
