import { Component, OnInit, Input } from '@angular/core';
import { Validators, FormGroup, FormBuilder } from '@angular/forms';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ModalConfirmationComponent } from '../modal-confirmation/modal-confirmation.component'
import { CustomerService } from '../services/customer.service';
import { UserService } from '../services/user.service';

@Component({
  selector: 'app-customer-user',
  templateUrl: './customer-user.component.html',
  styleUrls: ['./customer-user.component.css']
})
export class CustomerUserComponent implements OnInit {
  @Input() idCustomerFilter: number = null;
  submitted = false;
  submittedNewUser = false;

  public submittedNew: boolean;
  public showNewUser = false;
  public submittedEdit: boolean;
  public showEditUser = false;
  public customerUserList: any;
  public message: string;
  public companys: any[];
  public users: any[];
  customerUserForm: FormGroup;
  userCreateForm: FormGroup;
  userEditForm: FormGroup;
  public userRole: string;

  constructor(
    private modalConfirmation: NgbModal,
    public customerService: CustomerService,
    public userService: UserService,
    private formBuilder: FormBuilder
  ) { }

  ngOnInit() {
    var us = this.userService.getCurrentUser();
    this.userRole = "";
    this.userRole += us.localUser.userRoles.map(x => {
      return `${x.role.roleDescription}-`;
    });


    this.customerUserForm = this.formBuilder.group({
      customer: !!this.idCustomerFilter ? ['']: ['', Validators.required],
      user: ['', Validators.required]
    });

    this.userCreateForm = this.formBuilder.group({
      name: ['', Validators.required],
      email: ['', Validators.required],
      password: ['', Validators.required]
    });

    this.userEditForm = this.formBuilder.group({
      idUser: ['', Validators.required],
      name: ['', Validators.required],
      email: ['', Validators.required],
      password: ['']
    });
    

    this.loadUsersFree();
    this.loadData();
  }

  loadData() {
    let idCustomer: number = null;
    if (!!this.idCustomerFilter) idCustomer = this.idCustomerFilter;
    this.customerService.getCustomerUserList(idCustomer).then(x => {
      this.customerUserList = x.data;
    });
  }

  loadUsersFree() {
    this.userService.getUserWithCustomerRoleList().then(x => {
      this.users = x.data;
    });
  }

  onSubmit() {
    debugger;
    this.submitted = true;
    if (!!this.idCustomerFilter) this.customerUserForm.value.customer = this.idCustomerFilter;

    if (this.customerUserForm.invalid)
      return;
    this.submitted = false;
    this.customerService.createCustomerUser(this.customerUserForm.value).then(x => {
      if (x.successfully) {
        this.loadUsersFree();
        this.loadData();
      } else {
        this.message = x.reason;
      }
    });
  }

  close() {
    this.message = null;
  }

  newUser() {
    this.showNewUser = true;
    this.submittedNew = false;

    this.userCreateForm.setValue({
      name: '',
      email: '',
      password: '',
    });
  }

  editUser(customerExe) {
    debugger;
    this.showEditUser = true;
    this.submittedEdit = false;

    this.userEditForm.setValue({
      idUser: customerExe.user.idUser,
      name: customerExe.user.name,
      email: customerExe.user.email,
      password: '',
    });
  }

  setAdmin(customerExe) {
    customerExe.isAdmin = true;
    this.onSubmitSetAdmin(customerExe);
  }

  unsetAdmin(customerExe) {
    customerExe.isAdmin = false;
    this.onSubmitSetAdmin(customerExe);
  }

  onSubmitNewUser() {

    this.submittedNewUser = true;
    if (this.userCreateForm.invalid)
      return;

    var newUser = {
      "name": this.userCreateForm.value.name,
      "email": this.userCreateForm.value.email,
      "password": this.userCreateForm.value.password,
      "client": 1,
      "guidUser": '',
      "role": []
    };

    newUser.role.push(5);

    this.userService.createUser(newUser).then(x => {
      if (x.successfully) {

        this.customerUserForm.value.customer = this.idCustomerFilter
        this.customerUserForm.value.user = x.data.idUser;

        this.customerService.createCustomerUser(this.customerUserForm.value).then(y => {
          if (y.successfully) {
            this.showNewUser = false;
            this.submittedNewUser = false;
            this.loadData();
            this.loadUsersFree();
          } else {
            this.message = y.reason;
          }
        });
        
      }
    });
  }

  onSubmitEditUser() {

    this.submittedEdit = true;
    if (this.userEditForm.invalid)
      return;

    var editUser = {
      "idUser": this.userEditForm.value.idUser,
      "name": this.userEditForm.value.name,
      "email": this.userEditForm.value.email,
      "password": this.userEditForm.value.password,
      "idClient": 1,
    };

    this.userService.updateProfileUser(editUser).then(x => {
      if (x.successfully) {

        this.loadData();
        this.loadUsersFree();

        this.showEditUser = false;
        this.submittedEdit = false;

      }
    });
  }

  onSubmitSetAdmin(customerExe) {
    debugger;
    var setAdmin = {
      "idCustomerUser": customerExe.idCustomerUser,
      "idUser": customerExe.idUser,
      "idCustomer": customerExe.idCustomer,
      "isAdmin": customerExe.isAdmin
    };

    this.customerService.updateCustomerUser(setAdmin).then(x => {
      if (x.successfully) {

        this.loadData();
        this.loadUsersFree();

        this.showEditUser = false;
        this.submittedEdit = false;

      }
    });
  }

  get f() { return this.customerUserForm.controls; }
  get fNew() { return this.userCreateForm.controls; }
  get fdNewValid() { return this.userCreateForm.valid; }

  Delete(idCustomer, idUser) {
    const modalRef = this.modalConfirmation.open(ModalConfirmationComponent);
    modalRef.componentInstance.title = "Delete Record";
    modalRef.componentInstance.message = "Are you sure you want to unassigne the user to customer?";
    modalRef.result.then((result) => {
      if (result) {
        this.customerService.deleteCustomerUser(idCustomer, idUser)
          .then(x => {
            if (x.successfully) {
              this.loadData();
              this.loadUsersFree();
            } else {
              this.message = x.reason;
            }
          });
      }
    }).catch(x => { });
  }

}
