import { Component, OnInit, Input } from '@angular/core';
import { Validators, FormGroup, FormBuilder } from '@angular/forms';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ModalConfirmationComponent } from '../modal-confirmation/modal-confirmation.component'
import { InsuredService } from '../services/insured.service';
import { UserService } from '../services/user.service';

@Component({
  selector: 'app-insured-executive',
  templateUrl: './insured-executive.component.html',
  styleUrls: ['./insured-executive.component.css']
})
export class InsuredExecutiveComponent implements OnInit {
  @Input() idInsuredFilter: number = null;
  submitted = false;
  public insuredExecutiveList: any;
  public message: string;
  public companys: any[];
  public executives: any[];
  insuredExecutiveForm: FormGroup;

  constructor(
    private modalConfirmation: NgbModal,
    public insuredService: InsuredService,
    public userService: UserService,
    private formBuilder: FormBuilder
  ) { }

  ngOnInit() {
    this.insuredExecutiveForm = this.formBuilder.group({
      insured: !!this.idInsuredFilter ? ['']: ['', Validators.required],
      executive: ['', Validators.required]
    });

    this.insuredService.getCompanyList().then(x => {
      this.companys = x.data;
    });

    this.userService.getExecutiveList().then(x => {
      this.executives = x.data;
    });
    this.loadData();
  }

  loadData() {
    let idInsured: number = null;
    if (!!this.idInsuredFilter) idInsured = this.idInsuredFilter;
    this.insuredService.getInsuredExecutiveList(idInsured).then(x => {
      this.insuredExecutiveList = x.data;
    });
  }

  onSubmit() {
    this.submitted = true;
    if (!!this.idInsuredFilter) this.insuredExecutiveForm.value.insured = this.idInsuredFilter;

    if (this.insuredExecutiveForm.invalid)
      return;
    this.submitted = false;
    this.insuredService.createInsuredExecutive(this.insuredExecutiveForm.value).then(x => {
      if (x.successfully) {
        this.loadData();
      } else {
        this.message = x.reason;
      }
    });
  }

  close() {
    this.message = null;
  }

  get f() { return this.insuredExecutiveForm.controls; }

  Delete(idInsured, idExecutive) {
    const modalRef = this.modalConfirmation.open(ModalConfirmationComponent);
    modalRef.componentInstance.title = "Delete Record";
    modalRef.componentInstance.message = "Are you sure you want to delete the selected item?";
    modalRef.result.then((result) => {
      if (result) {
        this.insuredService.deleteInsuredExecutive(idInsured, idExecutive)
          .then(x => {
            if (x.successfully) {
              this.loadData();
            } else {
              this.message = x.reason;
            }
          });
      }
    }).catch(x => { });
  }

}
