import { Component, Directive, EventEmitter, Input, Output, QueryList, ViewChildren, OnInit } from '@angular/core';
import { QuoteService } from '../services/quote.service';
import { FormControl } from '@angular/forms';

interface Quote {
  idQuote: number;
  categoryDescription: string;
  insuredName: string;
  nameProvider: string;
  insuranceTypeDescription: string;
  shippingMethodDescription: string;
  totalQuote: number;
  premium: number;
}

export type SortColumn = keyof Quote | '';
export type SortDirection = 'asc' | 'desc' | '';
const rotate: { [key: string]: SortDirection } = { 'asc': 'desc', 'desc': '', '': 'asc' };

const compare = (v1: string | number, v2: string | number) => v1 < v2 ? -1 : v1 > v2 ? 1 : 0;

export interface SortEvent {
  column: SortColumn;
  direction: SortDirection;
}

@Directive({
  selector: 'th[sortable]',
  host: {
    '[class.asc]': 'direction === "asc"',
    '[class.desc]': 'direction === "desc"',
    '(click)': 'rotate()'
  }
})
export class NgbdSortableHeader {

  @Input() sortable: SortColumn = '';
  @Input() direction: SortDirection = '';
  @Output() sort = new EventEmitter<SortEvent>();

  rotate() {
    this.direction = rotate[this.direction];
    this.sort.emit({ column: this.sortable, direction: this.direction });
  }
}


@Component({
  selector: 'app-quote-requested-list',
  templateUrl: './quote-requested-list.component.html',
  styleUrls: ['./quote-requested-list.component.css']
})
export class QuoteRequestedListComponent implements OnInit {
  @Input() idInsuredFilter: number = null;
  @Input() idCustomerFilter: number = null;
  filter = new FormControl('');
  search = new FormControl('');
  public quoteRequested;
  public quoteRequestedFilter;
  public loading: boolean = false;
  page = 1;
  pageSize = 10;
  collectionSize;

  @ViewChildren(NgbdSortableHeader) headers: QueryList<NgbdSortableHeader>;

  constructor(public quoteService: QuoteService) { }

  ngOnInit() {
    this.loadData();
    this.filter.valueChanges.subscribe(value => {
      this.quoteRequestedFilter = this.findByFilter(value);
    });
    this.search.valueChanges.subscribe(value => {
      if (value.length == 0) {
        this.loadData();
      }
    });
  }

  loadData() {
    this.filter.setValue('');
    this.quoteService.getQuoteRequested('', this.idInsuredFilter, this.idCustomerFilter).then(x => {
      this.quoteRequested = x.data;
      this.quoteRequestedFilter = x.data;
      this.collectionSize = x.data.length;
      this.refreshQuotes();
    });
  }

  searchByFilter() {
    this.quoteService.getQuoteRequested(this.search.value, this.idInsuredFilter, this.idCustomerFilter).then(x => {
      this.quoteRequested = x.data;
      this.quoteRequestedFilter = x.data;
      this.collectionSize = x.data.length;
      this.refreshQuotes();
    });
  }

  findByFilter(text: string): any[] {
    return this.quoteRequested.filter(quote => {
      const term = text.toLowerCase();
      return quote.categoryDescription.toLowerCase().includes(term)
        || quote.insuredName.toLowerCase().includes(term)
        || quote.nameProvider.toLowerCase().includes(term)
        || quote.insuranceTypeDescription.toLowerCase().includes(term)
        || quote.shippingMethodDescription.toLowerCase().includes(term);
    }).slice((this.page - 1) * this.pageSize, (this.page - 1) * this.pageSize + this.pageSize);
  }

  refreshQuotes() {
    this.quoteRequestedFilter = this.quoteRequested
      .slice((this.page - 1) * this.pageSize, (this.page - 1) * this.pageSize + this.pageSize);
  }

  onSort({ column, direction }: SortEvent) {

    // resetting other headers
    this.headers.forEach(header => {
      if (header.sortable !== column) {
        header.direction = '';
      }
    });

    // sorting
    if (direction === '' || column === '') {
      this.refreshQuotes();
    } else {
      this.quoteRequestedFilter = this.quoteRequested
        .slice((this.page - 1) * this.pageSize, (this.page - 1) * this.pageSize + this.pageSize).sort((a, b) => {
          const res = compare(a[column], b[column]);
          return direction === 'asc' ? res : -res;
        });
    }
  }

  generatePDF(event, idQuote) {
    //
    this.loading = true;
    this.quoteService.GeneratePDFQuoteAccepted(idQuote)
      .then(x => {
        this.loading = false;
        if (x.successfully == true) {
          var sampleArr = this.base64ToArrayBuffer(Object.values(x.data)[0]);
          this.saveByteArray(Object.keys(x.data)[0], sampleArr);

          var sampleArr = this.base64ToArrayBuffer(Object.values(x.data)[1]);
          this.saveByteArray(Object.keys(x.data)[1], sampleArr);

          //this.urlRedirect = x.data.urlRedirect;

          //var ieEDGE = navigator.userAgent.match(/Edge/g);
          //var ie = navigator.userAgent.match(/.NET/g); // IE 11+
          //var oldIE = navigator.userAgent.match(/MSIE/g);

          //var bina = JSON.stringify(Object.values(x.data)[0]);

          //var blob = new window.Blob([bina], { type: 'application/pdf' });

          //if (ie || oldIE || ieEDGE) {

          //  var fileName = Object.keys(x.data)[0];
          //  window.navigator.msSaveBlob(blob, fileName);
          //}
          //else {

          //  var file = new Blob([bina], {
          //    type: 'application/pdf'
          //  });

          //  var fileURL = URL.createObjectURL(file);
          //  var a = document.createElement('a');
          //  a.href = fileURL;
          //  a.target = '_blank';
          //  a.download = Object.keys(x.data)[0];
          //  document.body.appendChild(a);

          //  a.click();

          //}
          //Successfully Downloaded

        }
      });


  }

  base64ToArrayBuffer(base64) {
    var binaryString = window.atob(base64);
    var binaryLen = binaryString.length;
    var bytes = new Uint8Array(binaryLen);
    for (var i = 0; i < binaryLen; i++) {
      var ascii = binaryString.charCodeAt(i);
      bytes[i] = ascii;
    }
    return bytes;
  }

  saveByteArray(reportName, byte) {
    var blob = new Blob([byte], { type: "application/pdf" });
    var link = document.createElement('a');
    link.href = window.URL.createObjectURL(blob);
    var fileName = reportName;
    link.download = fileName;
    link.click();
  };

  generatePDFCertificate(event, idQuote) {
    //
    this.loading = true;
    this.quoteService.GeneratePDFCertificate(idQuote)
      .then(x => {
        this.loading = false;
        if (x.successfully == true) {
          var sampleArr = this.base64ToArrayBuffer(Object.values(x.data)[0]);
          this.saveByteArray(Object.keys(x.data)[0], sampleArr);

        }
      });


  }

  emitCertificate(event, idQuote) {

  }

  showDetailtRow(obj) {
    console.log()

    var id = obj.srcElement.dataset.id;
    var row = document.getElementById('detail' + id);
    if (row.className == "visible") {
      row.className = 'hide';
    } else {
      row.className = 'visible';
    }

    for (var i = 0; i < obj.srcElement.parentElement.childNodes.length; i++) {
      if (obj.srcElement.parentElement.childNodes[i].className == "detailRow") {
        if (obj.srcElement.parentElement.childNodes[i].className == "visible") {
          obj.srcElement.parentElement.childNodes[i].removeClass('.visible');
        } else {
          obj.srcElement.parentElement.childNodes[i].addClass('.visible');
        }
        break;
      }
    }
    
  }
}


