import { Injectable, Inject } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { retry, catchError } from 'rxjs/operators';
import { UserService } from '../../app/services/user.service';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class QuoteFreeService {
  private baseApiUrl: string;

  constructor(
    private http: HttpClient, @Inject('BASE_API_URL') baseApiUrl: string,
    private userService: UserService,
    private router: Router
  ) {
    this.baseApiUrl = baseApiUrl;
  }

  //GET
  async get(): Promise<ResponseApi> {
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}QuickQuoteFree`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      debugger;
      await this.errorHandl(error);
    }
  }


  //POST
  async getQuoteRequested(object: any, idClient: number): Promise<ResponseApi> {
    try {
      let quote = {
        "idCategoryGood": +object.categoryGood,
        "idAvailableCoverage": +object.available,
        "idCurrency": +object.currency,
        "merchandiseValue": parseFloat(object.merchandiseValue.replace(',', '')),
        "freightValue": parseFloat(object.freightValue.replace(',', '')),
        "idInsuranceType": +object.insuranceLimit,
        "idClient": !!idClient ? +idClient : 0,
        "idCountryShipmentCommences": +object.countryShipCommence,
        "idCountryDestination": +object.countryDestination,
      };

      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}QuickQuoteFree/GetReviewFreeQuote`, JSON.stringify(quote), this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //POST
  async GeneratePDFFreeQuote(object: any, idClient: number, idProvider: number): Promise<ResponseApi> {
    try {
      let quote = {
        "idCategoryGood": +object.categoryGood,
        "idAvailableCoverage": +object.available,
        "idShippingMethod": +object.shippingMethod,
        "idCurrency": +object.currency,
        "merchandiseValue": parseFloat(object.merchandiseValue.replace(',', '')),
        "freightValue": parseFloat(object.freightValue.replace(',', '')),
        "idInsuranceType": +object.insuranceLimit,
        "idClient": !!idClient ? +idClient : 0,
        "idCountryShipmentCommences": +object.countryShipCommence,
        "idCountryDestination": +object.countryDestination,
        "idProvider": +idProvider
      };

      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}QuickQuoteFree/GeneratePDFFreeQuote`, JSON.stringify(quote), this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //GET
  async getInsuredByEmail(email): Promise<ResponseApi> {
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}QuickQuoteFree/GetInsuredByEmail?email=${email}`, this.userService.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //POST
  createInsured(insuredForm: any): Promise<ResponseApi> {
    let insured = {
      "idInsured": 0,
      "name": insuredForm.name,
      "idCountry": +insuredForm.country,
      "city": insuredForm.city,
      "address": insuredForm.address,
      "phone": insuredForm.phone.toString(),
      "email": insuredForm.email,
      "isConsignee": false,
      "isCompany": false,
      "contactPerson": insuredForm.contactPerson,
      "taxId": insuredForm.taxId,
      "zipCode": insuredForm.zipCode,
      "creditLimit": 0,
    }
    try {
      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}QuickQuoteFree/CreateInsured`, JSON.stringify(insured), this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  //POST
  createQuote(object: any, idClient: number, idProvider: number, idInsured: number): Promise<ResponseApi> {
    let quote = {
      "idCategoryGood": +object.categoryGood,
      "idAvailableCoverage": +object.available,
      "idShippingMethod": +object.shippingMethod,
      "idCurrency": +object.currency,
      "merchandiseValue": parseFloat(object.merchandiseValue.replace(',', '')),
      "freightValue": parseFloat(object.freightValue.replace(',', '')),
      "idInsuranceType": +object.insuranceLimit,
      "idClient": !!idClient ? +idClient : 0,
      "idCountryShipmentCommences": +object.countryShipCommence,
      "idCountryDestination": +object.countryDestination,
      "idProvider": +idProvider,
      "idInsured": +idInsured,
      "increaseCountryValue": 0,
      "descriptionDetailsOfGood": '',
      "additionalComments": ''
    };
    try {
      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}QuickQuoteFree/CreateQuote`, JSON.stringify(quote), this.userService.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }



  // Error handling
  errorHandl(error) {
    let errorMessage = '';
    if (error.error instanceof ErrorEvent) {
      // Get client-side error
      errorMessage = error.error.message;
    } else {
      // Get server-side error
      errorMessage = `Error Code: ${error.status}\nMessage: ${error.message}`;
    }
    console.log(errorMessage);

    if (error.status == 401) {
      this.userService.signOut();
      this.router.navigate(['login']);
    }

    return throwError(errorMessage);
  }

  buildJson(insuredForm, shippingForm, cargoForm, idClient, idInsured, idConsignee) {
    let shippingDate = this.buildDateShipping(shippingForm.shippingDate)
    return {
      "idCategoryGood": +cargoForm.categoryGood,
      "descriptionDetailsOfGood": cargoForm.descriptionGood,
      "idCurrency": +cargoForm.currency,
      "merchandiseValue": this.removeFormatNumber(cargoForm.merchandiseValue),
      "freightValue": this.removeFormatNumber(cargoForm.freightValue),
      "invoiceValue": cargoForm.invoiceValue,
      "increaseCountryValue": 0,
      "idInsuranceType": +cargoForm.insuranceLimit,
      "idClient": idClient,
      "idInsured": +idInsured,
      "idConsignee": +idConsignee,
      "lossPayee": insuredForm.lossPayee,
      "idTypeShipment": +shippingForm.typeShipment,
      "vesselName": shippingForm.vesselName,
      "additionalCarrierInformation": shippingForm.addicionalCarrier,
      "shippingDate": shippingDate,
      "placeShipmentCommences": shippingForm.placeShipCommence,
      "idCountryShipmentCommences": +shippingForm.countryShipCommence,
      "idCountryOfPortLoading": +shippingForm.countryPortLoading,
      "portOfLoading": shippingForm.portLoading,
      "finalDestinationOfShipment": shippingForm.finalDestination,
      "idCountryDestination": +shippingForm.countryDestination,
      "idCountryPortOfDischarge": +shippingForm.countryPortDischarge,
      "portOfDischarge": shippingForm.portDischarge,
      "idShippingMethod": +cargoForm.shippingMethod,
      "idAvailableCoverage": +cargoForm.available,
      "markAndNumber": cargoForm.markNumber,
      "piecesAndWeight": cargoForm.pieceWeight,
      "additionalComments": cargoForm.addicionalComment
    };
  }

  buildDateShipping(date) {
    return date.year + '-' + this.zfill(date.month, 2) + '-' + this.zfill(date.day, 2);
  }

  removeFormatNumber(value) {
    return parseFloat(value.toString().replace(',', ''));
  }

  zfill(number, width) {
    var numberOutput = Math.abs(number);
    var length = number.toString().length;
    var zero = "0";

    if (width <= length)
      return numberOutput.toString();
    else
      return ((zero.repeat(width - length)) + numberOutput.toString());
  }

}

interface ResponseApi {
  successfully: boolean
  reason: string
  data: any
  dataOptional: any
}

interface Quote {
  categoryGood: number
  currency: number
  merchandiseValue: number
  freightValue: number
  insuranceLimit: number
}
