import { Injectable, Inject } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { BehaviorSubject, Observable, throwError, of } from 'rxjs';
import { AuthService } from "angularx-social-login";
import { map } from 'rxjs/operators';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class UserService {
  private baseApiUrl: string;
  private globalUser: BehaviorSubject<any>;
  private localUser: any;

  constructor(
    private http: HttpClient,
    @Inject('BASE_API_URL') baseApiUrl: string,
    private authService: AuthService,
    private router: Router
  ) {
    this.baseApiUrl = baseApiUrl;
    this.globalUser = new BehaviorSubject<any>(null);
  }

  httpOptions = {
    headers: new HttpHeaders({
      'Content-Type': 'application/json'
    })
  }

  //GET
  async loginUser(loginForm): Promise<ResponseApi> {
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}User/LoginUser?email=${loginForm.email}&password=${loginForm.password}`)
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //GET
  async getMyProfile(): Promise<ResponseApi> {
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}User/GetMyProfile`, this.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //POST
  getUserByEmail(user: any): Promise<ResponseApi> {
    try {
      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}User/GetUserByEmail`, { "userName": user.name, "userEmail": user.email })
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  //GET
  async getUserByGuid(guid): Promise<ResponseApi> {
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}User/GetUserByGuid?guid=${guid}`)
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //GET
  async getUserByFilter(filter): Promise<ResponseApi> {
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}User/GetUserByFilter?filter=${filter}`, this.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //GET
  async getUserById(id): Promise<ResponseApi> {
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}User/GetUserById?id=${id}`, this.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //GET
  async getUsersByClient(idClient): Promise<ResponseApi> {
    try {
      let params = "";
      if (!!idClient) {
        params = `$?idClient = ${idClient}`
      }
      let response = await this.http
        .get(`${this.baseApiUrl}User/GetUsersByClient` + params, this.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //POST
  updateUser(user: any, userEdit: any): Promise<ResponseApi> {
    let userObject = this.buildUserObject(user, userEdit);
    try {
      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}User/SynchronizeUser`, JSON.stringify(userObject), this.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  //POST
  updateProfileUser(user: any): Promise<ResponseApi> {
    let userObject = user;
    try {
      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}User/UpdateUserProfile`, JSON.stringify(userObject), this.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  updateUserItem(user: any): Promise<ResponseApi> {
    try {
      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}User/SynchronizeUser`, JSON.stringify(user), this.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  //POST
  createUser(user: any): Promise<ResponseApi> {
    let userObject = this.buildUserObject(user, null);
    try {
      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}User/SynchronizeUser`, JSON.stringify(userObject), this.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  //GET
  async getExecutiveList(): Promise<ResponseApi> {
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}User/GetExecutiveList`, this.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //GET
  async getUserWithCustomerRoleList(): Promise<ResponseApi> {
    try {

      let response = await this.http
        .get(`${this.baseApiUrl}User/GetUserWithCustomerRoleList`, this.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //GET
  async getSupervisorList(): Promise<ResponseApi> {
    try {
      let response = await this.http
        .get(`${this.baseApiUrl}User/GetSupervisorList`, this.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //POST
  updateProfile(mailToSupervisor: boolean): Promise<ResponseApi> {
    try {
      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}User/UpdateProfile?mailToSupervisor=${mailToSupervisor}`, null, this.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  buildUserObject(user, userEdit) {
    return {
      "idUser": userEdit != null ? userEdit.idUser : 0,
      "name": user.name,
      "email": user.email,
      "password": user.password,
      "idClient": +user.client,
      "guidUser": user.guidUser,
      "userRoles": user.role.map(x => {
        return {
          "idUser": userEdit != null ? userEdit.idUser : 0,
          "idRole": +x
        };
      })
    }
  }

  // User Supervisor //
  //POST
  createUserSupervisor(formValue: any): Promise<ResponseApi> {
    let userSupervisor = { "idUser": +formValue.executive, "idSupervisor": +formValue.supervisor };
    try {
      let response = this.http
        .post<ResponseApi>(`${this.baseApiUrl}UserSupervisor/CreateUserSupervisor`, JSON.stringify(userSupervisor), this.buildHeader())
        .toPromise();
      return response;
    } catch (error) {
      this.errorHandl(error);
    }
  }

  //GET
  async getUserSupervisorList(idUserSupervisor: number = null): Promise<ResponseApi> {
    try {
      let idSuperStr = '';
      if (!!idUserSupervisor) idSuperStr = `/${idUserSupervisor}`
      let response = await this.http

        .get(`${this.baseApiUrl}UserSupervisor/GetUserSupervisorList${idSuperStr}`, this.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  //DELETE
  async deleteUserSupervisor(idUser: number, idSupervisor: number): Promise<ResponseApi> {
    try {
      let response = await this.http
        .delete(`${this.baseApiUrl}UserSupervisor/DeleteUserSupervisor?idUser=${idUser}&idSupervisor=${idSupervisor}`, this.buildHeader())
        .toPromise();
      return response as ResponseApi;
    } catch (error) {
      await this.errorHandl(error);
    }
  }

  signOut(): void {
    if (this.localUser.socialUser != null) {
      this.authService.signOut();
    }
    this.setUser(null);
    localStorage.removeItem("currentUser");
  }

  setUser(user) {
    if (user != null) {
      localStorage.setItem("currentUser", JSON.stringify(user));
    }
    this.localUser = user;
    this.globalUser.next(user);
  }

  getCurrentUser() {
    let user = localStorage.getItem("currentUser");
    if (user != null) {
      return JSON.parse(user);
    }
  }

  getToken() {
    let localUser = localStorage.getItem("currentUser");
    if (localUser != null) {
      let user = JSON.parse(localUser);
      return user.localUser.token;
    }
  }

  buildHeader() {
    return { headers: new HttpHeaders().set('Content-Type', 'application/json').set('Authorization', `Bearer ${this.getToken()}`) };
  }

  authState(): Observable<any> {
    return this.globalUser.asObservable();
  }

  // Error handling
  errorHandl(error) {
    let errorMessage = '';
    if (error.error instanceof ErrorEvent) {
      // Get client-side error
      errorMessage = error.error.message;
    } else {
      // Get server-side error
      errorMessage = `Error Code: ${error.status}\nMessage: ${error.message}`;
    }
    console.log(errorMessage);

    if (error.status == 401) {
      this.signOut();
      this.router.navigate(['login']);
    }

    return throwError(errorMessage);
  }
}

interface ResponseApi {
  successfully: boolean
  reason: string
  data: any
}

interface UserInterface {
  email: string
  idClient: string
}
