import { Injectable } from '@angular/core';
import { ProviderService } from '../../services/provider.service';
import { DropdownQuestion } from './question-dropdown';
import { QuestionBase } from './question-base';
import { QuestionGroupBase } from './question-group-base';
import { TextboxQuestion } from './question-textbox';
import { TextareaQuestion } from './question-textarea';
import { DatetimeQuestion } from './question-datetime';
import { of, Observable } from 'rxjs';

@Injectable()
export class QuestionService {
  constructor(
    public providerService: ProviderService
  ) { }

  // TODO: get from a remote source of question metadata
  async getQuestions() {

    var questions: QuestionBase<string>[] = [

      new DropdownQuestion({
        key: 'brave',
        label: 'Bravery Rating',
        options: [
          { key: 'solid', value: 'Solid' },
          { key: 'great', value: 'Great' },
          { key: 'good', value: 'Good' },
          { key: 'unproven', value: 'Unproven' }
        ],
        order: 3
      }),

      new TextboxQuestion({
        key: 'firstName',
        label: 'First name',
        value: 'Bombasto',
        required: true,
        order: 1
      }),

      new TextboxQuestion({
        key: 'emailAddress',
        label: 'Email',
        type: 'email',
        order: 2
      }),

      new TextboxQuestion({
        key: 'lastName',
        label: 'Last Name',
        order: 2
      })
    ];



    await this.providerService.getProviderGroupFormList(2)
      .then(x => {
        if (!!x.data) {
          x.data.forEach(group => {
            questions.push(new TextboxQuestion({
              key: group.idProviderGroupForm.toString(),
              label: group.title,
              required: true,
              order: 1
            }));
          });
        }
      });

    questions.push(new TextboxQuestion({
      key: '11',
      label: 'aaa',
      required: true,
      order: 1
    }));

    return of(questions.sort((a, b) => a.order - b.order));
  }

  async getQuestionsGroup(idQuote): Promise<Observable<QuestionGroupBase[]>> {
    var questionsGroup: QuestionGroupBase[] = [];
    
    await this.providerService.getProviderGroupFormListByIdQuote(idQuote)
      .then(x => {
        if (!!x.successfully && x.data.length > 0) {
          x.data.forEach(group => {

            var questionsValue: QuestionBase<string>[] = [];
            group.providerGroupFormFieldList.forEach(field => {

              switch (field.type) {
                case "textbox":
                  questionsValue.push(new TextboxQuestion({
                    key: field.idProviderGroupFormField.toString(),
                    label: field.title,
                    required: field.isRequired,
                    order: field.orderField,
                    value: !!field.value ? field.value : field.defaultValue
                  }));
                  break;

                case "textarea":
                  questionsValue.push(new TextareaQuestion({
                    key: field.idProviderGroupFormField.toString(),
                    label: field.title,
                    required: field.isRequired,
                    order: field.orderField,
                    value: !!field.value ? field.value : field.defaultValue
                  }));
                  break;

                case "datetime":
                  questionsValue.push(new DatetimeQuestion({
                    key: field.idProviderGroupFormField.toString(),
                    label: field.title,
                    required: field.isRequired,
                    order: field.orderField,
                    value: !!field.value ? field.value : field.defaultValue
                  }));
                  break;

                case "dropdown":

                  var optionsArray: { key: string, value: string }[] = [];

                  field.propValues.toString().split("|").forEach(optionValue => {
                    optionsArray.push({ key: optionValue, value: optionValue });
                  });


                  questionsValue.push(

                    new DropdownQuestion({
                      key: field.idProviderGroupFormField.toString(),
                      label: field.title,
                      options: optionsArray,
                      required: field.isRequired,
                      order: field.orderField,
                      value: !!field.value ? field.value : field.defaultValue
                    })
                  );
                  break;
              }
            });

            questionsGroup.push(new QuestionGroupBase({
              key: group.idProviderGroupForm.toString(),
              title: group.title,
              order: group.orderGroup,
              questions: questionsValue.sort((a, b) => a.order - b.order),
              classNameGroup: 'col-md-' + group.colSize
            }))
          });

          return of(questionsGroup.sort((a, b) => a.order - b.order));
        } else {
          return of(questionsGroup);
        }
      });
    return of(questionsGroup);
  }
}


