import { Component, OnInit, Input } from '@angular/core';
import { Validators, FormGroup, FormBuilder } from '@angular/forms';
import { Options } from 'select2';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ModalConfirmationComponent } from '../../modal-confirmation/modal-confirmation.component'
import { PremiumSetupService } from '../../services/premium-setup.service';
import { CategoryGoodService } from '../../services/category-good.service';
import { AvailableCoverageService } from '../../services/available-coverage.service';
import { CommissionService } from '../../services/commission.service';
import { ProviderService } from '../../services/provider.service';
import { ClientService } from '../../services/client.service';
import { FormControl } from '@angular/forms';
import { BusinessQuoteService } from '../../services/business-quote.service';

import { InsuredService } from '../../services/insured.service';
import { ResponseApi } from '../../_models/ResponseApi';

@Component({
  selector: 'app-adm-premium-commission',
  templateUrl: './adm-premium-commission.component.html',
  styleUrls: ['./adm-premium-commission.component.css']
})
export class AdmPremiumCommissionComponent implements OnInit {
  @Input() idCustomerFilter: number = null;
  @Input() allowEdit: boolean = true;
  @Input() showCommissionType: boolean = false;
  title = "";
  actItem = false;
  editItem: number = 0;
  loadingBqs = false;
  submitted = false;
  premiumForm: FormGroup;
  optionsSelect: Options;
  categoryGood: any = [];
  availableCovers: any[];
  providerList: any[];
  commissionList: any[];
  clientList: any[];
  reason: string;
  filter = new FormControl('');
  numCols: number = 8;

  public premiumCommission;
  public premiumCommissionFilter;

  constructor(
    public premiumSetupService: PremiumSetupService,
    public availableService: AvailableCoverageService,
    public commissionService: CommissionService,
    public providerService: ProviderService,
    public clientService: ClientService,
    public categoryService: CategoryGoodService,
    public businessQuoteService: BusinessQuoteService,
    private formBuilder: FormBuilder,
  ) { }

  ngOnInit() {

    if (this.showCommissionType && this.allowEdit) {
      this.numCols = 8;
    } else if (this.showCommissionType && !this.allowEdit) {
      this.numCols = 7;
    } else if (!this.showCommissionType && this.allowEdit) {
      this.numCols = 7;
    } else if (!this.showCommissionType && !this.allowEdit) {
      this.numCols = 6;
    }

    this.premiumForm = this.formBuilder.group({
      provider: ['', Validators.required],
      available: ['', Validators.required],
      categoryGood: ['', Validators.required],
      client: ['', Validators.required],
      commission: ['', Validators.required],
      rateValue: ['', Validators.required],
      idCustomer: ['', Validators.required],
      idPremiumCommission: ['']
    });

    this.categoryService.getCategoryGood()
      .then(x => {
        this.categoryGood = [{ id: '', text: '' }];
        x.data.forEach(category => {
          this.categoryGood.push({ id: category.idCategoryGood, text: category.description });
        });
      });

    this.availableService.getAvailableCoverage()
      .then(x => this.availableCovers = x.data);

    this.providerService.getProviderList()
      .then(x => this.providerList = x.data);

    this.commissionService.getCommissionList()
      .then(x => this.commissionList = x.data);

    this.clientService.getClientList()
      .then(x => this.clientList = x.data);

    this.optionsSelect = {
      placeholder: "Select option...",
      allowClear: true,
      width: "100%"
    };

    this.loadData();

    this.filter.valueChanges.subscribe(value => {
      debugger;
      this.premiumCommissionFilter = this.findByFilter(value);
    });
  }

  loadData() {
    this.filter.setValue('');
    this.premiumSetupService.GetPremiumCommissionListOfCustomer(+this.idCustomerFilter).then(x => {
      debugger;
      this.premiumCommission = x.data;
      this.premiumCommissionFilter = x.data;
    });

  }

  findByFilter(text: string): any[] {
    return this.premiumCommission.filter(premium => {
      const term = text.toLowerCase();
      return premium.providerName.toLowerCase().includes(term)
        || premium.availableCoverageDescription.toLowerCase().includes(term)
        || premium.categoryGoodDescription.toLowerCase().includes(term)
        || premium.clientName.toLowerCase().includes(term)
        || premium.commissionName.toLowerCase().includes(term);
    });
  }

  get f() { return this.premiumForm.controls; }

  cancel() {
    this.title = '';
    this.editItem = 0;
    this.actItem = false;
  }

  new() {
    this.title = 'New';
    this.editItem = 0;
    this.actItem = true;

    this.premiumForm.get("idPremiumCommission").setValue(this.editItem);
    this.premiumForm.get("idCustomer").setValue(+this.idCustomerFilter);
  }

  edit(idPremiumCommission) {
    this.title = 'Edit';
    this.actItem = true;
    this.editItem = idPremiumCommission;

    this.premiumSetupService.GetPremiumCommission(+idPremiumCommission).then(x => {
      var premium = x.data;
      this.premiumForm.get("provider").setValue(premium.idProvider);
      this.premiumForm.get("available").setValue(premium.idAvailableCoverage);
      this.premiumForm.get("categoryGood").setValue(premium.idCategoryGood);
      this.premiumForm.get("client").setValue(premium.idClient);
      this.premiumForm.get("commission").setValue(premium.idCommission);
      this.premiumForm.get("rateValue").setValue(premium.rateValue);
      this.premiumForm.get("idPremiumCommission").setValue(premium.idPremiumCommission);
      this.premiumForm.get("idCustomer").setValue(premium.idCustomer);
    });
  }

  onReset() {
    this.submitted = false;
    this.premiumForm.reset();

    this.premiumForm.get("idPremiumCommission").setValue(this.editItem);
    this.premiumForm.get("idCustomer").setValue(+this.idCustomerFilter);
  }

  delete(idPremiumCommission) {
    debugger;
    this.premiumSetupService.DeletePremiumCommission(+idPremiumCommission).then(x => {
      if (x.successfully) {
        this.loadData();
      }
    });
  }

  onSubmit() {
    this.submitted = true;

    if (this.premiumForm.invalid) {
      return;
    }

    if (this.editItem != 0) {
      this.updatePremium();
    } else {
      this.createPremium();
    }
  }

  createPremium() {
    this.premiumSetupService.createPremiumCommission2(this.premiumForm.value)
      .then(x => this.evaluateResult(x));
  }

  updatePremium() {
    debugger;
    this.premiumSetupService.updatePremiumCommission2(this.premiumForm.value)
      .then(x => this.evaluateResult(x));
  }

  evaluateResult(result: ResponseApi) {
    if (result.successfully == false) {
      this.reason = result.reason;
    } else {
      this.onReset();
      this.cancel();
      this.loadData();
    }
  }

  loadAllBQsOfComission(idPremiumCommission, premium) {
    debugger;
    this.loadingBqs = true;
    this.businessQuoteService.getBusinessQuoteDetailByCommission(+idPremiumCommission).then(x => {
      debugger;
      this.loadingBqs = false;
      if (x.successfully) {
        premium.BQs = x.data;
      }
    });
  }

  showDetailtRow(obj) {

    var id = obj.srcElement.dataset.id;
    var row = document.getElementById('detail' + id);
    if (row.className == "visible") {
      row.className = 'hide';
    } else {
      row.className = 'visible';
    }

    for (var i = 0; i < obj.srcElement.parentElement.childNodes.length; i++) {
      if (obj.srcElement.parentElement.childNodes[i].className == "detailRow") {
        if (obj.srcElement.parentElement.childNodes[i].className == "visible") {
          obj.srcElement.parentElement.childNodes[i].removeClass('.visible');
        } else {
          obj.srcElement.parentElement.childNodes[i].addClass('.visible');
        }
        break;
      }
    }

  }
}
