import { Component, OnInit, Input } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { BusinessQuoteService } from '../services/business-quote.service';

@Component({
  selector: 'app-business-quote-modal',
  templateUrl: './business-quote-modal.component.html',
  styleUrls: ['./business-quote-modal.component.css']
})
export class BusinessQuoteModalComponent implements OnInit {
  @Input() isModal: boolean;
  @Input() idBusinessQuote: number;
  @Input() idExecutive: number;
  @Input() readOnly: boolean;
  public loading: boolean = false;
  public successSend: boolean = false;
  public businessQuoteEdit: any = null;
  public showModal: boolean = true;


  constructor(public activeModal: NgbActiveModal, public bQuoteService: BusinessQuoteService) {
  }

  ngOnInit() {
    this.bQuoteService.GetBusinessQuoteById(this.idBusinessQuote).then(x => {
      if (x.successfully) {
        this.businessQuoteEdit = x.data;
      }
    });
  }

  sendPDFBusinessQuote(event) {
    //
    this.loading = true;
    this.bQuoteService.SendPDFBusinessQuote(this.idBusinessQuote)
      .then(x => {
        this.loading = false;
        this.successSend = x.successfully;
      });
  }

  cancelBQuote(event) {
    //
    this.loading = true;
    this.bQuoteService.CancelBusinessQuote(this.idBusinessQuote)
      .then(x => {
        this.loading = false;
        this.businessQuoteEdit = x.data;
        this.showModal = false;
        setTimeout(() => { this.showModal = true }, 300);
      });
  }

  generatePDFBusinessQuote(event) {
    //
    this.loading = true;
    this.bQuoteService.GeneratePDFBusinessQuote(this.idBusinessQuote)
      .then(x => {
        this.loading = false;
        debugger;
        if (x.successfully == true) {
          /*
          var sampleArr = this.base64ToArrayBuffer(Object.values(x.data)[0]);
          this.saveByteArray(Object.keys(x.data)[0], sampleArr);

          var sampleArr2 = this.base64ToArrayBuffer(Object.values(x.data)[1]);
          this.saveByteArray(Object.keys(x.data)[1], sampleArr2);
          */
          var files = Object.keys(x.data);
          for (var i = 0; i < files.length; i++) {
            var sampleArr = this.base64ToArrayBuffer(Object.values(x.data)[i]);
            this.saveByteArray(Object.keys(x.data)[i], sampleArr);
          }
        }
      });
  }



  base64ToArrayBuffer(base64) {
    var binaryString = window.atob(base64);
    var binaryLen = binaryString.length;
    var bytes = new Uint8Array(binaryLen);
    for (var i = 0; i < binaryLen; i++) {
      var ascii = binaryString.charCodeAt(i);
      bytes[i] = ascii;
    }
    return bytes;
  }

  saveByteArray(reportName, byte) {
    var blob = new Blob([byte], { type: "application/pdf" });
    var link = document.createElement('a');
    link.href = window.URL.createObjectURL(blob);
    var fileName = reportName;
    link.download = fileName;
    link.click();
  };

}
