import { Component, OnInit, Input } from '@angular/core';
import { CommissionService } from '../services/commission.service';
import { FormBuilder, FormGroup, Validators, FormArray } from '@angular/forms';

@Component({
  selector: 'app-commission-distrib',
  templateUrl: './commission-distrib.component.html',
  styleUrls: ['./commission-distrib.component.css']
})
export class CommissionDistribComponent implements OnInit {
  @Input() isModal = false;
  @Input() idCommission;
  public commiDistribList: any;
  submitted = false;
  public enableEdit = false;
  public enableEditIndex = null;
  public message: string;
  commissionForm: FormGroup;
  commissionEditForm: FormGroup;

  constructor(
    public commissionService: CommissionService,
    private formBuilder: FormBuilder,
  ) { }

  ngOnInit() {
    this.commissionForm = this.formBuilder.group({
      idParent: [''],
      nameDistrib: ['', Validators.required],
      percentage: ['', Validators.required]
    });
    this.commissionEditForm = this.formBuilder.group({
      idParent: [''],
      nameDistrib: ['', Validators.required],
      percentage: ['', Validators.required]
    });
    this.loadData();
  }
  get f() { return this.commissionForm.controls; }
  get fdValid() { return this.commissionEditForm.valid; }

  loadData() {
    this.commissionService.getCommissionDistributions(this.idCommission).then(x => {
      this.commiDistribList = x.data;
    });
  }

  enableEditMethod(e, i, distrib) {
    this.enableEdit = true;
    this.enableEditIndex = i;
    this.commissionEditForm.setValue({
      idParent: distrib.idParent,
      nameDistrib: distrib.nameDistribution,
      percentage: distrib.percentage
    });
  }

  Cancel() {
    this.enableEdit = false;
    this.enableEditIndex = null;
  }

  New() {
    this.submitted = true;
    if (this.commissionForm.invalid)
      return;
    this.submitted = false;
    this.commissionService.createCommissionDistribution(this.commissionForm.value, this.idCommission)
      .then(x => {
        if (x.successfully) {
          this.message = null;
          this.commissionForm.reset();
          this.loadData();
        } else {
          this.message = x.reason;
        }
      });
  }

  Delete(idCommissionDistribution) {
    this.commissionService.DeleteCommissionDistribution(idCommissionDistribution, this.idCommission)
      .then(x => {
        if (x.successfully)
          this.loadData();
      });
  }

  SaveChange(idCommissionDistribution) {
    this.commissionService.updateCommissionDistribution(this.commissionEditForm.value, idCommissionDistribution)
      .then(x => {
        if (x.successfully) {
          this.Cancel();
          this.loadData();
        }
      });
  }
}
