import { Component, OnInit } from '@angular/core';
import { UserService } from '../../services/user.service';
import { Router } from "@angular/router";

@Component({
  selector: 'app-cus-rates',
  templateUrl: './cus-rates.component.html',
  styleUrls: ['./cus-rates.component.css']
})
export class CusRatesComponent implements OnInit {
  public titlePage: string = "Rates";
  public loading: boolean = true;
  public idCustomer: number;
  constructor(
    private userService: UserService,
    private router: Router,
  ) { }

  ngOnInit() {
    this.loading = true;
    var us = this.userService.getMyProfile().then(x => {
      if (!x.data.isCustomerUser) {
        this.router.navigate(['/']);
      } else {
        this.idCustomer = x.data.idCustomer;
        this.loading = false;
      }
    });
  }

}

