import { Component, OnInit } from '@angular/core';
import { AuthService } from "angularx-social-login";
import { GoogleLoginProvider } from "angularx-social-login";
import { FormBuilder, FormGroup, Validators, FormControl, FormArray } from '@angular/forms';
import { NgbModal, ModalDismissReasons, NgbModalRef  } from '@ng-bootstrap/ng-bootstrap';
import { CategoryGoodService } from '../../services/category-good.service';
import { ModalConfirmationComponent } from '../../modal-confirmation/modal-confirmation.component'
import { Router } from '@angular/router';

@Component({
  selector: 'app-adm-category-good',
  templateUrl: './adm-category-good.component.html',
  styleUrls: ['./adm-category-good.component.css']
})
export class AdmCategoryGoodComponent implements OnInit {
  public submitted: boolean;
  public submittedNew: boolean;
  categoryGoodListForm: FormGroup;
  categoryGoodCreateForm: FormGroup;
  public showMessage: boolean = false;
  public categoryGoodList: any = [];
  public categoryGoodListFilter: any = [];
  public enableEdit = false;
  public enableEditIndex = null;
  public categoryGoodFilter;
  modalNew: NgbModalRef;
  filter = new FormControl('');
  closeResult = '';

  constructor(
    private authService: AuthService,
    private formBuilder: FormBuilder,
    private categoryGoodService: CategoryGoodService,
    private router: Router,
    private modalService: NgbModal,
    private modalConfirmation: NgbModal
  ) { }

  ngOnInit() {
    this.categoryGoodListForm = this.formBuilder.group({
      description: ['', Validators.required],
      coverageDescription: ['']
    });
    this.categoryGoodCreateForm = this.formBuilder.group({
      description: ['', Validators.required],
      coverageDescription: ['']
    });


    this.loadData();
    this.filter.valueChanges.subscribe(value => {
      this.categoryGoodListFilter = this.search(value);
    });

  }

  get f() { return this.categoryGoodListForm.controls; }
  get fNew() { return this.categoryGoodCreateForm.controls; }
  get fdValid() { return this.categoryGoodListForm.valid; }
  get fdNewValid() { return this.categoryGoodCreateForm.valid; }

  loadData() {
    this.categoryGoodService.getCategoryGood()
      .then(x => {
        this.categoryGoodList = x.data;
        this.categoryGoodListFilter = this.search(this.filter.value);
      });
  }

  search(text: string): any[] {
    return this.categoryGoodList.filter(categoryGood => {
      const term = text.toLowerCase();
      return categoryGood.description.toLowerCase().includes(term)
        || categoryGood.coverageDescription.toLowerCase().includes(term);
    });
  }

  onSubmit() {
    this.submitted = true;
    this.showMessage = false;
    if (this.categoryGoodListForm.invalid) {
      return;
    }
  }

  enableEditMethod(e, i, categoryGoodRow) {
    this.enableEdit = true;
    this.enableEditIndex = i;
    this.categoryGoodListForm.setValue({
      description: categoryGoodRow.description,
      coverageDescription: categoryGoodRow.coverageDescription
    });
  }

  Cancel() {
    this.enableEdit = false;
    this.enableEditIndex = null;
  }

  Delete(idCategoryGood) {
    const modalRef = this.modalConfirmation.open(ModalConfirmationComponent);
    modalRef.componentInstance.title = "Delete Record";
    modalRef.componentInstance.message = "Are you sure you want to delete the selected item?";
    modalRef.result.then((result) => {
      if (result) {
        this.categoryGoodService.deleteCategoryGood(idCategoryGood)
          .then(x => {
            if (x.successfully)
              this.loadData();
          });
      }
    }).catch(x => { });

    
  }

  SaveChange(idCategoryGood) {
    this.categoryGoodService.updateCategoryGood(idCategoryGood, this.categoryGoodListForm.value)
      .then(x => {
        if (x.successfully) {
          this.Cancel();
          this.loadData();
        }
      });
  }

  New(content) {
    this.ClearNew();
    this.modalNew = this.modalService.open(content, { ariaLabelledBy: 'modal-basic-title' });
    this.modalNew.result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }

  ClearNew() {
    this.categoryGoodCreateForm.setValue({
      description: '',
      coverageDescription: ''
    });
  }

  onSubmitNew() {
    debugger;
    this.submittedNew = true;

    if (this.categoryGoodCreateForm.invalid) {
      return;
    }

    this.CreateNew();
  }
  CreateNew() {
    this.categoryGoodService.createCategoryGood(this.categoryGoodCreateForm.value)
      .then(x => {
        if (x.successfully) {
          this.modalNew.close();
          this.ClearNew();
          this.loadData();
        }
      });
  }
}

