import { Component, OnInit, Input } from '@angular/core';
import { CommissionService } from '../services/commission.service';
import { FormControl, Validators, FormGroup, FormBuilder } from '@angular/forms';
import { NgbModal, NgbActiveModal, NgbModalConfig } from '@ng-bootstrap/ng-bootstrap';
import { ModalConfirmationComponent } from '../modal-confirmation/modal-confirmation.component'
import { Router } from '@angular/router';

@Component({
  selector: 'validation-comi-modal-content',
  template: `
    <div class="modal-body">
      <form [formGroup]="validationForm">
        <div class="form-group">
          <label for="pass">Enter Password</label>
          <input type="password" class="form-control" id="pass" formControlName="pass" />
        </div>
      </form>
    </div>
    <div class="modal-footer">
      <button type="button" class="btn btn-outline-dark" (click)="activeModal.close(f)">Send</button>
    </div>
  `
})
export class ValidationComiModalContent {
  validationForm: FormGroup;
  constructor(config: NgbModalConfig,
    public activeModal: NgbActiveModal,
    private formBuilder: FormBuilder) {
    config.backdrop = 'static';
    config.keyboard = false;
  }
  ngOnInit() {
    this.validationForm = this.formBuilder.group({
      pass: ['']
    });
  }
  get f() { return this.validationForm.get("pass").value; }
}

@Component({
  selector: 'commission-modal-content',
  template: `
    <div class="modal-body">
      <app-commission-distrib [isModal]="isModal" [idCommission]="idCommission" ></app-commission-distrib>
    </div>
    <div class="modal-footer">
      <button type="button" class="btn btn-outline-dark" (click)="activeModal.close('Close click')">Close</button>
    </div>
  `
})
export class CommissionModalContent {
  @Input() isModal: boolean;
  @Input() idCommission: number;
  constructor(public activeModal: NgbActiveModal) {}
}

@Component({
  selector: 'app-commission-setup',
  templateUrl: './commission-setup.component.html',
  styleUrls: ['./commission-setup.component.css']
})
export class CommissionSetupComponent implements OnInit {
  public commissionList: any;
  submitted = false;
  public enableEdit = false;
  public enableEditIndex = null;
  public message: string;
  commissionName = new FormControl('', Validators.required);
  commissionNameEdit = new FormControl('', Validators.required);

  constructor(
    public commissionService: CommissionService,
    private modalConfirmation: NgbModal,
    private modalDistribution: NgbModal,
    private modalValidationComi: NgbModal,
    private router: Router
  ) { }

  ngOnInit() {
    //const modalRef = this.modalValidationComi.open(ValidationComiModalContent);
    //modalRef.result.then((result) => {
    //  if (result != "abc123456") {
    //    this.router.navigate(['/']);
    //  }
    //}).catch(x => { });
    this.loadData();
  }

  loadData() {
    this.commissionService.getCommissionList().then(x => {
      this.commissionList = x.data;
    });
  }

  New() {
    this.submitted = true;
    if (this.commissionName.invalid)
      return;
    this.submitted = false;
    this.commissionService.createCommission(this.commissionName.value)
      .then(x => {
        this.commissionName.setValue('');
        this.loadData();
      })
  }

  enableEditMethod(e, i, nameCommission) {
    this.enableEdit = true;
    this.enableEditIndex = i;
    this.commissionNameEdit.setValue(nameCommission);
  }

  SaveChange(idCommission) {
    this.commissionService.updateCommission(idCommission, this.commissionNameEdit.value)
      .then(x => {
        this.enableEdit = false;
        this.enableEditIndex = null;
        this.loadData();
      });
  }

  Delete(idCommission) {
    const modalRef = this.modalConfirmation.open(ModalConfirmationComponent);
    modalRef.componentInstance.title = "Delete Record";
    modalRef.componentInstance.message = "Are you sure you want to delete the selected item?";
    modalRef.result.then((result) => {
      if (result) {
        this.commissionService.DeleteCommission(idCommission)
          .then(x => {
            if (x.successfully) {
              this.loadData();
            } else {
              this.message = x.reason;
            }
          });
      }
    }).catch(x => { });
  }

  Cancel() {
    this.enableEdit = false;
    this.enableEditIndex = null;
  }

  close() {
    this.message = null;
  }

  CommissionDistrib(idCommission) {
    const modalRef = this.modalDistribution.open(CommissionModalContent);
    modalRef.componentInstance.isModal = true;
    modalRef.componentInstance.idCommission = idCommission;
    modalRef.result.then(() => {  }).catch(x => { });
  }

}
