import { Component, OnInit, Input } from '@angular/core';
import { QuoteService } from '../services/quote.service';
import { ActivatedRoute } from "@angular/router";
import { UserService } from '../services/user.service';

@Component({
  selector: 'app-quote-requested',
  templateUrl: './quote-requested.component.html',
  styleUrls: ['./quote-requested.component.css']
})
export class QuoteRequestedComponent implements OnInit {
  public quoteId = null;
  public quoteInfo;
  public currentUser: any;
  public userRole: string;
  @Input() showHeader: boolean = true;
  @Input() quoteIdByRoute: boolean = true;
  @Input() quoteIdParam: number;

  constructor(public quoteService: QuoteService,
    private route: ActivatedRoute,
    private userService: UserService) { }

  ngOnInit() {
    this.loadData();
    this.currentUser = this.userService.getCurrentUser();
    if (this.currentUser != null) {
      this.verifyUserRole();
    }
  }

  loadData() {
    if (this.quoteIdByRoute) {
      this.quoteId = this.route.snapshot.paramMap.get("quoteId")
    } else if (this.quoteIdParam != null) {
      this.quoteId = this.quoteIdParam;
    }

    if (this.quoteId != null) {
      debugger;
      this.quoteService.getQuoteById(this.quoteId).then(result => {
        if (result != null) {
          this.quoteInfo = result.data;
        }
      });
    }
  }

  verifyUserRole() {
    if (this.currentUser != null) {
      if (this.currentUser.localUser != null && this.currentUser.localUser.userRoles != null) {
        this.userRole = "";
        this.userRole += this.currentUser.localUser.userRoles.map(x => {
          return `${x.role.roleDescription}-`;
        });
      } else {
        this.userRole = "";
      }
    }
  }

}
